let App = require("App")
let CryptoJS = require("CryptoJS")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        account: {
            default: null,
            type: cc.EditBox
        },
        password: {
            default: null,
            type: cc.EditBox
        },
        rememberPassword: {
            default: null,
            type: cc.Toggle
        },
        security: {
            default: null,
            type: cc.EditBox
        },
        bgSecurity: {
            default: null,
            type: cc.Sprite
        },
        getSecurityCode: {
            default: null,
            type: cc.Button
        },
        aeskey: "qianren2019",
        loginErrorCount: 0
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        cc.debug.setDisplayStats(false)

        cc.view.setOrientation(cc.macro.ORIENTATION_LANDSCAPE)

        let self = this

        let account = this.account.getComponent(cc.EditBox)
        let password = this.password.getComponent(cc.EditBox)
        let localAccount = cc.sys.localStorage.getItem("account")
        let localPassword = cc.sys.localStorage.getItem("password")
        if (localAccount != null) {
            let rawAccount = CryptoJS.AES.decrypt(localAccount, this.aeskey).toString(CryptoJS.enc.Utf8)
            account.string = rawAccount
        }
        if (localPassword != null) {
            let rawPassword = CryptoJS.AES.decrypt(localPassword, this.aeskey).toString(CryptoJS.enc.Utf8)
            password.string = rawPassword
        }

        if (cc.sys.localStorage.getItem("loginErrorCount") != null) {
            self.loginErrorCount = parseInt(cc.sys.localStorage.getItem("loginErrorCount"))
        }
        if (self.loginErrorCount >= 3) {
            self.bgSecurity.node.active = true
            self.tapGetSecurityCode()
        }

        this.bgSecurity.node.active = false

        App.get("/auth/countValidError", function (data) {
            if (parseInt(data.code) == 200) {
                App.token = data.data.token
                if (data.data.showCaptcha == true) {
                    self.bgSecurity.node.active = true
                } else {
                }
            }
        })
        // let writablePath = jsb.fileUtils.getWritablePath();
        // let path = writablePath + "test.png"
        // let url = "http://192.168.0.82:8080/heroesports/partylogo/02bd979de74097486068094b23afe41e.png"
        // cc.log(path,url)
        // mycc.HttpHelper.getInstance().fetch(url,path,function(ok,filename){
        //     cc.log(ok,filename)
        // })
    },

    start() {
        //cc.find('Loading').getComponent("Loading").close()
    },

    tapGetSecurityCode(sender, customEventData) {
        let self = this
        cc.loader.load({ url: App.getSecurityCodeUrl(), type: "png" }, function (err, texture) {
            let sp = new cc.SpriteFrame(texture)
            let background = self.getSecurityCode.node.getChildByName("Background")
            background.getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture)
        });
    },

    tapClear(sender, customEventData) {
        cc.sys.localStorage.removeItem("account")
        cc.sys.localStorage.removeItem("password")
        let account = this.account.getComponent(cc.EditBox)
        let password = this.password.getComponent(cc.EditBox)
        account.string = ""
        password.string = ""
    },

    tapLogin(sender, customEventData) {
        let self = this
        let account = this.account.getComponent(cc.EditBox)
        let password = this.password.getComponent(cc.EditBox)
        let security = this.security.getComponent(cc.EditBox)

        if (account.string == "") {
            cc.find("FadeMsg").getComponent("FadeMsg").show("用户名不能为空")
            return
        }

        if (this.password.string == "") {
            cc.find("FadeMsg").getComponent("FadeMsg").show("密码不能为空")
            return
        }

        if(self.bgSecurity.node.active == true && security.string == ""){
            cc.find("FadeMsg").getComponent("FadeMsg").show("验证码不能为空")
            return
        }

        cc.find('Loading').getComponent("Loading").show()
        let postData = {
            "captcha": security.string,
            "password": password.string,
            "username": account.string,
            "token": App.token,
        }

        App.post("/auth/login", postData, function (data) {
            if (parseInt(data.code) == 200) {
                if (self.rememberPassword.getComponent(cc.Toggle).isChecked) {
                    let saveAccount = String(CryptoJS.AES.encrypt(account.string, self.aeskey))
                    let savePassword = String(CryptoJS.AES.encrypt(password.string, self.aeskey))
                    cc.sys.localStorage.setItem("account", saveAccount)
                    cc.sys.localStorage.setItem("password", savePassword)
                }
                App.myUser.setup(data.data)
                App.requestToken = data.data.token
                self.loginErrorCount = 0
                cc.sys.localStorage.setItem("loginErrorCount", 0)
                cc.director.loadScene("HallScene")

                //cc.log(data)
            }
            else {
                cc.log(JSON.stringify(data))
                cc.find('Loading').getComponent("Loading").close()
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                self.loginErrorCount++
                cc.sys.localStorage.setItem("loginErrorCount", self.loginErrorCount)
                if (self.loginErrorCount >= 3) {
                    self.bgSecurity.node.active = true
                    self.tapGetSecurityCode()
                }
            }
        })

       
    },

    tapRegister(sender, customEventData) {
        cc.director.loadScene("RegisterScene")
    },

    checkRemberPassword(sender, customEventData) {

    },

    tapForgetPassword(sender, customEventData) {
        cc.director.loadScene("ResetPasswordScene")
    },

    tapButton(sender,customEventData){
        cc.director.loadScene("HallScene")
    }

    // update (dt) {},
});
