//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        friendList: {
            default: null,
            type: cc.ScrollView
        },
        friendItem: {
            default: null,
            type: cc.Prefab
        },
        inviteInfoList: {
            default: null,
            type: cc.ScrollView
        },
        inviteInfoItem: {
            default: null,
            type: cc.Prefab
        },
        recentlyPartyList: {
            default: null,
            type: cc.ScrollView
        },
        recentlyPartyItem: {
            default: null,
            type: cc.Prefab
        },
        addFriend: {
            default: null,
            type: cc.Node
        },
        searchFriendList:{
            default: null,
            type: cc.ScrollView
        },
        searchFriendItem:{
            default: null,
            type:cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })
        this.friendList.node.active = true
        this.inviteInfoList.node.active = false
        this.recentlyPartyList.node.active = false
        this.addFriend.active = false
        for (let i = 0; i < 10; i++) {
            this.friendList.content.addChild(cc.instantiate(this.friendItem))
        }
    },

    start() {

    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    checkFriend(sender, customEventData) {
        let index = parseInt(customEventData)
        switch (index) {
            case 1:
                {
                    this.friendList.node.active = true
                    this.inviteInfoList.node.active = false
                    this.recentlyPartyList.node.active = false
                    this.addFriend.active = false
                    for (let i = 0; i < 10; i++) {
                        this.friendList.content.addChild(cc.instantiate(this.friendItem))
                    }
                }
                break
            case 2:
                {
                    this.friendList.node.active = false
                    this.inviteInfoList.node.active = true
                    this.recentlyPartyList.node.active = false
                    this.addFriend.active = false
                    for (let i = 0; i < 10; i++) {
                        this.inviteInfoList.content.addChild(cc.instantiate(this.inviteInfoItem))
                    }
                }
                break
            case 3:
                {
                    this.friendList.node.active = false
                    this.inviteInfoList.node.active = false
                    this.recentlyPartyList.node.active = true
                    this.addFriend.active = false
                    for (let i = 0; i < 10; i++) {
                        this.recentlyPartyList.content.addChild(cc.instantiate(this.recentlyPartyItem))
                    }
                }
                break
            case 4:
                {
                    this.friendList.node.active = false
                    this.inviteInfoList.node.active = false
                    this.recentlyPartyList.node.active = false
                    this.addFriend.active = true
                    for (let i = 0; i < 10; i++) {
                        this.searchFriendList.content.addChild(cc.instantiate(this.searchFriendItem))
                    }
                }
                break
        }
    }

    // update (dt) {},
});
