//  - [English] http://docs.cocos2d-x.org/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

let App = require("App")
let Tools = require("Tools")
let gameLogo = require("gameLogo")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        gameList: {
            default: null,
            type: cc.ScrollView
        },
        gameItem: {
            default: null,
            type: cc.Node
        },
        gameCategoryMask: {
            default: null,
            type: cc.Sprite
        },
        gameCategory: {
            default: null,
            type: cc.ToggleContainer
        },
        gameCategoryItem: {
            default: null,
            type: cc.Node
        },
        previousGameCategoryItem: {
            default: null,
            type: cc.Node
        },
        gameCategoryItemArray: [cc.Prefab],
        expandGameCategory: {
            default: null,
            type: cc.Toggle
        },
        betPanel: {
            default: null,
            type: cc.Prefab
        },
        week: {
            default: null,
            type: cc.ToggleContainer
        },
        weekCount: 0,
        today: {
            default: null,
            type: cc.Toggle
        },
        checkTypeType: 1,
        category: null,
        startDate: "",
        gameType: {
            default: null,
            type: cc.ToggleContainer
        }
    },

    // LIFE-CYCLE CALLBACKS:

    //state的取值对应是0-竞猜中,1-比赛中,2-已取消,3-已暂停,4-已结束,5-已结算
    //除了滚盘 比赛中是可以投注的，其余的只能是竞猜中才能投注


    onLoad() {
        this.gameCategory.node.removeAllChildren()

        let self = this
        App.get("/games/category", function (data) {
            if (parseInt(data.code) == 200) {
                self.updateGameCategory(data.data)
                self.expandGameCategory.getComponent(cc.Toggle).uncheck()
            }
        })

        this.requestCheckType()

        let today = new Date()
        self.showWeek(today)

        this.today.node.on('click', function (button) {
            self.weekCount = 0
            self.showWeek(today)
            self.week.node.getChildByName("toggle1").getComponent(cc.Toggle).check()
        })

        this.gameCategoryItem.active = false
        this.gameItem.active = false

        cc.find("Canvas").getComponent("HallScene").showSettingButton(false)
    },

    showWeek(date) {
        for (let i = 0; i < 7; i++) {
            let newDate = new Date(date.getTime() + i * 24 * 60 * 60 * 1000)
            let year = newDate.getFullYear()
            let month = newDate.getMonth() + 1
            let day = newDate.getDate()
            let dayStr = month + "-" + day + " " + Tools.getWeekStr(year, month, day)
            this.week.node.getChildByName("toggle" + (i + 1)).getChildByName("label").getComponent(cc.Label).string = dayStr

            var checkEventHandler = new cc.Component.EventHandler();
            checkEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            checkEventHandler.component = "moreGame"
            checkEventHandler.handler = "checkWeek";
            checkEventHandler.customEventData = {
                "date": year + "-" + month + "-" + day,
                "index": i
            }

            this.week.node.getChildByName("toggle" + (i + 1)).getComponent(cc.Toggle).checkEvents.push(checkEventHandler);
        }
        this.week.node.getChildByName("toggle1").getComponent(cc.Toggle).check()
    },

    checkWeek(sender, customEventData) {
        this.startDate = customEventData.date
        for (let i = 0; i < 7; i++) {
            if (i == customEventData.index && sender.isChecked) {
                this.week.node.getChildByName("toggle" + (i + 1)).getChildByName("label").color = new cc.Color(36, 158, 210)
            } else {
                this.week.node.getChildByName("toggle" + (i + 1)).getChildByName("label").color = new cc.Color(167, 173, 175)
            }
        }

        this.requestCheckType()
    },

    requestCheckType() {
        this.gameList.content.removeAllChildren()
        let self = this
        let postData
        switch (this.checkTypeType) {
            //今日
            case 1:
                {
                    postData = {
                        "startDate": Tools.todayStr() + " " + Tools.timeStr(),
                        "endDate": Tools.nextDateStr(),
                        "status": 0,
                        "type": 0,
                        "offset": 0,
                        "category": this.category,
                        "categoryType": 0
                    }
                }
                break
            //滚盘
            case 2:
                {
                    postData = {
                        "state": 1,
                        "type": 0,
                        "offset": 0,
                        "category": this.category,
                        "categoryType": 0
                    }
                }
                break
            //冠军
            case 3:
                {
                    postData = {
                        "startDate": this.startDate,
                        "state": 0,
                        "type": 1,
                        "offset": 0,
                        "category": this.category,
                        "categoryType": 0
                    }
                }
                break
            //已结束
            case 4:
                {
                    postData = {
                        "state": 4,
                        "type": 0,
                        "offset": 0,
                        "limit": 30,
                        "category": this.category,
                        "categoryType": 0
                    }
                }
                break
        }
        cc.find('Loading').getComponent("Loading").show()
        App.post("/games/list", postData, function (data) {
            cc.find('Loading').getComponent("Loading").close()
            if (parseInt(data.code) == 200) {
                self.updateMoreGame(data.data)
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },

    checkType(sender, customEventData) {
        switch (parseInt(customEventData)) {
            //今日
            case 1:
                {
                    this.weekCount = 0
                    this.checkTypeType = 1
                    let today = new Date()
                    this.showWeek(today)
                }
                break
            //滚盘
            case 2:
                {
                    this.checkTypeType = 2
                }
                break
            //冠军
            case 3:
                {
                    this.checkTypeType = 3
                }
                break
            //已结束
            case 4:
                {
                    this.checkTypeType = 4
                }
                break
        }

        for (let i = 1; i <= 4; i++) {
            if (i == parseInt(customEventData)) {
                this.gameType.node.getChildByName("toggle" + i).getChildByName("name").color = new cc.Color(255, 255, 255)
            } else {
                this.gameType.node.getChildByName("toggle" + i).getChildByName("name").color = new cc.Color(137, 203, 255)
            }
        }

        this.requestCheckType()
    },

    updateGameCategory(data) {
        this.gameCategory.node.removeAllChildren()

        //全部项
        let sum = 0
        for (let i = 0; i < data.length; i++) {
            sum += (data[i].total)
        }
        {
            let gameCategoryItem = cc.instantiate(this.gameCategoryItem)
            gameCategoryItem.active = true
            let title = gameCategoryItem.getChildByName("title")
            let total = gameCategoryItem.getChildByName("total")
            gameCategoryItem.setPosition(cc.v2(0, 0))
            title.getComponent(cc.Label).string = "全部"
            total.getComponent(cc.Label).string = 100
            this.gameCategory.node.addChild(gameCategoryItem)
            this.gameCategoryItemArray.push(gameCategoryItem)
        }

        for (let i = 0; i < data.length; i++) {
            let gameCategoryItem = cc.instantiate(this.gameCategoryItem)
            gameCategoryItem.active = true
            let stepX = (i + 1) % 5
            let setpY = parseInt((i + 1) / 5)
            let pos = cc.v2(stepX * 212, setpY * (-60))
            gameCategoryItem.setPosition(pos)
            this.gameCategory.node.addChild(gameCategoryItem)
            this.gameCategoryItemArray.push(gameCategoryItem)

            let title = gameCategoryItem.getChildByName("title")
            let total = gameCategoryItem.getChildByName("total")

            //cc.log(gameLogo[i + 1].unselected)
            cc.loader.loadRes(gameLogo[i + 1].unselected, cc.SpriteFrame, function (err, spriteFrame) {
                //cc.log(i+1,spriteFrame)
                if (err) {
                    return
                }
                gameCategoryItem.getChildByName("logo").getComponent(cc.Sprite).spriteFrame = spriteFrame
            })

            title.getComponent(cc.Label).string = data[i].title
            total.getComponent(cc.Label).string = data[i].total

            title.color = new cc.Color(36, 158, 210)
        }

        for (let i = 0; i < this.gameCategoryItemArray.length; i++) {
            var checkEventHandler = new cc.Component.EventHandler();
            checkEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            checkEventHandler.component = "moreGame"
            checkEventHandler.handler = "checkGameCategoryItem";
            if (i == 0) {
                checkEventHandler.customEventData = {
                    "category": null,
                    "categoryType": 0,
                    "index": i
                }
            } else {
                checkEventHandler.customEventData = {
                    "category": data[i - 1].id,
                    "categoryType": data[i - 1].sport,
                    "index": i
                }
            }
            let toggle = this.gameCategoryItemArray[i].getComponent(cc.Toggle)
            toggle.checkEvents.push(checkEventHandler);
        }
    },

    checkGameCategoryItem(sender, customEventData) {
        let self = this
        this.category = customEventData.category
        this.requestCheckType()
        if (sender.isChecked) {
            sender.node.getChildByName("title").color = new cc.Color(255, 255, 255)
            cc.loader.loadRes(gameLogo[customEventData.index].selected, cc.SpriteFrame, function (err, spriteFrame) {
                if (err) {
                    return
                }
                sender.node.getChildByName("logo").getComponent(cc.Sprite).spriteFrame = spriteFrame
            })
        } else {
            sender.node.getChildByName("title").color = new cc.Color(36, 158, 210)
        }
        if (this.previousGameCategoryItem != null) {
            this.previousGameCategoryItem.node.getChildByName("title").color = new cc.Color(36, 158, 210)
        }

        for (let i = 0; i < this.gameCategoryItemArray.length; i++) {
            if (i !== customEventData.index) {
                cc.loader.loadRes(gameLogo[i].unselected, cc.SpriteFrame, function (err, spriteFrame) {
                    if (err) {
                        return
                    }
                    self.gameCategoryItemArray[i].getChildByName("logo").getComponent(cc.Sprite).spriteFrame = spriteFrame
                })
            }
        }

        this.previousGameCategoryItem = sender
    },

    start() {

    },

    tapBack(sender, customEventData) {
        this.node.destroy()
    },

    checkExpandGameCategory(sender, customEventData) {
        if (sender.isChecked) {
            for (let i = 0; i < this.gameCategoryItemArray.length; i++) {
                this.gameCategoryItemArray[i].active = true
            }
            let height = 62 + (parseInt(this.gameCategoryItemArray.length / 5) * 60)
            this.gameCategoryMask.node.setContentSize(1214, height)
            this.expandGameCategory.node.getChildByName("Background").active = false
        }
        else {
            for (let i = 0; i < 5; i++) {
                this.gameCategoryItemArray[i].active = true
            }
            for (let i = 5; i < this.gameCategoryItemArray.length; i++) {
                this.gameCategoryItemArray[i].active = false
            }
            this.gameCategoryMask.node.setContentSize(1214, 62)
            this.expandGameCategory.node.getChildByName("Background").active = true
        }
    },

    updateMoreGame(data) {
        this.gameList.content.removeAllChildren()
        //cc.log(data)
        for (let i = 0; i < data.content.length; i += 2) {
            let gameItem = cc.instantiate(this.gameItem)
            gameItem.active = true
            gameItem.setPosition(cc.v2(0, 0))
            this.gameList.content.addChild(gameItem)

            let bg0 = gameItem.getChildByName("bg_competition_item0")
            let bg1 = gameItem.getChildByName("bg_competition_item1")

            this.setupGameItem(data, bg0, i)
            if (i + 1 < data.content.length) {
                this.setupGameItem(data, bg1, i + 1)
            }
            else {
                bg1.active = false
            }
        }
    },
    setupGameItem(data, root, index) {
        let partyLogo0 = root.getChildByName("icon_party_logo0")
        let partyLogo1 = root.getChildByName("icon_party_logo1")

        let url0
        if (/*cc.sys.isBrowser*/false) {
            let regStr = "www\.nmgdjkj\.com/file/(\\w+\.png)"
            let reg = new RegExp(regStr)
            let result = data.content[index].teams[0].eimage.match(reg)
            url0 = App.getEimage(result[1])
        } else {
            url0 = "https://" + data.content[index].teams[0].eimage
        }
        cc.loader.load({ url: url0, type: "png" }, function (err, texture) {
            let sp = new cc.SpriteFrame(texture)
            partyLogo0.getComponent(cc.Sprite).spriteFrame = sp
        });

        /*var img = document.createElement("img")
        img.src = url0
        img.src = 'res/import/0c/0cbc18ab-2689-4384-ae8a-ade2126a6357.png'
        cc.log("img",img)
        img.onload = function () {
            let texture = new cc.Texture2D();
            texture.initWithElement(img);
            texture.handleLoadedTexture();
            partyLogo0.getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture);
        };
        img.style.display="none"
        document.body.appendChild(img)*/

        let url1
        if (/*cc.sys.isBrowser*/false) {
            let regStr = "www\.nmgdjkj\.com/file/(\\w+\.png)"
            let reg = new RegExp(regStr)
            let result = data.content[index].teams[1].eimage.match(reg)
            url1 = App.getEimage(result[1])
        } else {
            url1 = "https://" + data.content[index].teams[1].eimage
        }
        cc.loader.load({ url: url1, type: "png" }, function (err, texture) {
            let sp = new cc.SpriteFrame(texture)
            partyLogo1.getComponent(cc.Sprite).spriteFrame = sp
        });

        let partyName0 = root.getChildByName("partyName0")
        let partyName1 = root.getChildByName("partyName1")
        let name = root.getChildByName("name")
        let state = root.getChildByName('state')

        partyName0.getComponent(cc.Label).string = data.content[index].teams[0].name
        partyName1.getComponent(cc.Label).string = data.content[index].teams[1].name
        name.getComponent(cc.Label).string = data.content[index].name
        state.getComponent(cc.Label).string = Tools.GetGameStateStr([data.content[index].state])

        let buttonNode = root.getChildByName("button")
        let clickEventHandler = new cc.Component.EventHandler();
        clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
        clickEventHandler.component = "moreGame";// 这个是代码文件名
        clickEventHandler.handler = "tapGame";
        if (data.content[index].matches.length > 0) {
            clickEventHandler.customEventData = {
                "gameId": data.content[index].id,
                "matchId": data.content[index].matches[0].id
            }
        }else{
            clickEventHandler.customEventData = {
                "gameId": data.content[index].id,
                "matchId": undefined
            }
        }

        var button = buttonNode.getComponent(cc.Button);
        button.clickEvents.push(clickEventHandler);

    },

    tapGame(sender, customEventData) {
        let getData = [
            "id=" + customEventData.gameId
        ]
        let self = this
        App.getWithData("/games/detail", getData, function (data) {
            if (parseInt(data.code) == 200) {
                let betPanel = cc.instantiate(self.betPanel)
                cc.find("Canvas").addChild(betPanel)
                betPanel.getComponent("bet").setupWithData(data.data, customEventData.gameId,customEventData.matchId)
                //self.node.destroy()
            }
        })
    },

    tapPreviousWeek(sender, customEventData) {
        this.weekCount++
        let today = new Date()
        this.showWeek(new Date(today.getTime() + this.weekCount * 7 * 24 * 60 * 60 * 1000))
    },

    tapNextWeek(sender, customEventData) {
        this.weekCount--
        let today = new Date()
        this.showWeek(new Date(today.getTime() + this.weekCount * 7 * 24 * 60 * 60 * 1000))
    },

    // update (dt) {},

    onDestroy() {
        cc.find("Canvas").getComponent("HallScene").showSettingButton(true)
    }
});
