//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
let Tools = require('Tools')
let App = require("App")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        mask: {
            default: null,
            type: cc.Sprite
        },

        bg: {
            default: null,
            type: cc.Sprite
        },

        receiveAwardPanel: {
            default: null,
            type: cc.Prefab
        },
        dayArray: [cc.Node],
        signedToday: {
            default: null,
            type: cc.Label
        },
        continuousSignSize: {
            default: null,
            type: cc.Label
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        /*let self = this
        this.bg.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            event.stopProgagation()
        })*/

        let self = this
        this.mask.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            self.node.destroy()
        })

        let today = new Date()
        let year = today.getFullYear()
        let month = today.getMonth() + 1
        let firstDayWeek = Tools.getWeek(year, month, 1)
        let monthDays = Tools.getMonthDays(year, month)
        let flag = false
        this.dayArray.push(undefined)
        for (let i = 0; i < 36; i++) {
            let day = this.node.getChildByName("day" + i)
            day.active = false
        }
        for (let i = 0, j = 1; j <= monthDays; i++) {
            let day = this.node.getChildByName("day" + i)
            if (i == firstDayWeek) {
                flag = true
            }
            if (flag) {
                day.active = true
                j++
                this.dayArray.push(day)
            }
        }
        for (let i = 1; i < this.dayArray.length; i++) {
            this.dayArray[i].getComponent(cc.Label).string = i;
        }

        this.dayArray[today.getDate()].color = new cc.Color(255, 0, 0)

        App.getWithToken("/promotions/checkSignin", function (data) {
            if (parseInt(data.code) == 200) {
                if (data.data.signedToday) {
                    self.signedToday.getComponent(cc.Label).string = "今日已签到"
                }
                else {
                    self.signedToday.getComponent(cc.Label).string = "今日未签到"
                }
                self.continuousSignSize.getComponent(cc.Label).string = "已连续签到" + data.data.continuousSignSize + "天"
                for (let i = 0; i < data.data.signInDays.length; i++) {
                    cc.loader.loadRes("icon_online", cc.SpriteFrame, function (err, spriteFrame) {
                        if (err) {
                            cc.log(err)
                            return
                        }
                        self.dayArray[data.data.signInDays[i]].getChildByName("signin").getComponent(cc.Sprite).spriteFrame = spriteFrame
                    })
                }
            }
        })
    },

    start() {

    },

    // update (dt) {},

    tapReceive(sender, customEventData) {
        let receiveAwardPanel = cc.instantiate(this.receiveAwardPanel)
        cc.find("Canvas").addChild(receiveAwardPanel)
        receiveAwardPanel.getComponent("receiveAward").setCount(5)
    },
    tapSignin(sender, customEventData) {
        let self = this
        let postData = [
            "categoryId=" + 5
        ]
        App.postTitle("/tasks", postData, function (data) {
            if (parseInt(data.code) == 200) {
                cc.find("FadeMsg").getComponent("FadeMsg").show("签到成功")
                self.signedToday.getComponent(cc.Label).string = "今日已签到"
                cc.loader.loadRes("icon_online", cc.SpriteFrame, function (err, spriteFrame) {
                    if (err) {
                        cc.log(err)
                        return
                    }
                    let today = new Date()
                    self.dayArray[today.getDate()].getChildByName("signin").getComponent(cc.Sprite).spriteFrame = spriteFrame
                })
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },
    tapGetSigninReward(sender, customEventData) {
        App.postNoneWithToken('/promotions/applySignin', function (data) {
            cc.log(data)
            if (true/parseInt(data.code) == 200) {
                cc.find("FadeMsg").getComponent("FadeMsg").show("领取签到优惠成功")
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    }
});
