
var App = require('App');

cc.Class({
    extends: cc.Component,

    properties: {
        
        signBg: cc.SpriteFrame,
        closeBtn: cc.Node,
        alert: cc.Node,
        list: [cc.Node]
    },

    onLoad () {

        App.getWithToken('/promotions/checkSignin', (d)=> {

            this.showSignin(d.data.totalSignSize);
            if(!d.data.applyTotalSignToday) this.showCurSignin(d.data.totalSignSize);
        })

        this.alert.children[0].on('touchend', ()=> {

            this.alert.active = false
        })

        this.closeBtn.on('touchend', ()=> {

            this.node.destroy()
        })
    },


    showSignin(d) {

        for(var i = 0; i < d; i++) {

            this.list[i].children[2].getComponent(cc.Label).string = '已领取';
        }
    },


    showCurSignin(d) {

        this.list[d].children[2].color = cc.color('#20CAFB');
        this.list[d].getComponent(cc.Sprite).spriteFrame = this.signBg;
        this.list[d].once('touchend', ()=> {

            let xhr = new XMLHttpRequest()
            xhr.open("POST", App.apiurl + '/promotions/applySignin')
            xhr.setRequestHeader("Authorization", "Baer " + App.requestToken)
            xhr.send()
            xhr.onreadystatechange = ()=> {
                
                App.do_onreadystatechange(xhr, (d)=> {

                    if(!cc.isValid(this)) return;

                    if(d.code != 200) {

                        return cc.find("FadeMsg").getComponent("FadeMsg").show(d.message)
                    }

                    // cc.find("FadeMsg").getComponent("FadeMsg").show('签到成功');
                    this.list[d].children[2].getComponent(cc.Label).string = '已领取';
                    this.list[d].children[2].color = cc.color('#79A2D3');

                    this.alert.active = true;
                })
            }
        })
    }

});
