let SDKTools = require("SDKTools")
let App = require("App")
cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        shareUrl: {
            default: null,
            type: cc.Label
        },
        qrcode: {
            default: null,
            type: cc.Sprite
        },
        shareUrlStr: "https://fir.im/8knv"
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        let self = this
        this.shareUrl.string = "下载链接:  " + this.shareUrlStr
        //！！！无法off带参数的事件
        App.once("finishCreateQRImage", function(url, qrCodeSavePath, width, height){
            //cc.log("222 finishCreateQRImage", url, qrCodeSavePath, width, height)
            cc.loader.load(qrCodeSavePath, function (err, texture) {
                //cc.log(err,texture)
                if (err) {
                    return
                }
                let sp = new cc.SpriteFrame(texture)
                self.qrcode.spriteFrame = new cc.SpriteFrame(texture)
            });
        })

        SDKTools.createQRImage(this.shareUrlStr, 300, 300)
    },

    start() {

    },

    onClose() {
        this.node.destroy()
    },

    tapCopyToClipBoard(sender, customEventData) {
        SDKTools.copyToClipBoard(this.shareUrlStr)  
    },

    tapSaveToPhotoAlbum(sender, customEventData) {
        SDKTools.saveToPhotoAlbum()
    },
});
