let App = require("App")
let Tools = require("Tools")
let SDKToos = require("SDKTools")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        audioSetting: {
            default: null,
            type: cc.Node
        },
        accountPassword: {
            default: null,
            type: cc.Node
        },
        strongboxPassword: {
            default: null,
            type: cc.Node
        },
        strongboxFirstPassword: {
            default: null,
            type: cc.Node
        },
        strongboxModifyPassword: {
            default: null,
            type: cc.Node
        },
        bindTelephone: {
            default: null,
            type: cc.Node
        },
        bindDebitCard: {
            default: null,
            type: cc.Node
        },
        aboutus: {
            default: null,
            type: cc.Node
        },
        bindDebitCardList: {
            default: null,
            type: cc.ScrollView
        },
        bindDebitCardItem: {
            default: null,
            type: cc.Node
        },
        accountOldPassword: {
            default: null,
            type: cc.EditBox
        },
        accountNewPassword: {
            default: null,
            type: cc.EditBox
        },
        accountConfirmPassword: {
            default: null,
            type: cc.EditBox
        },
        /*accountSecurityCode: {
            default: null,
            type: cc.EditBox
        },
        accountGetSecurityCode: {
            default: null,
            type: cc.Button
        },*/
        strongboxFirstNewPassword: {
            default: null,
            type: cc.EditBox
        },
        strongboxFirstConfirmPassword: {
            default: null,
            type: cc.EditBox
        },
        strongboxModifyOldPassword: {
            default: null,
            type: cc.EditBox
        },
        strongboxModifyNewPassword: {
            default: null,
            type: cc.EditBox
        },
        strongboxModifyConfirmPassword: {
            default: null,
            type: cc.EditBox
        },
        shakeProgressBar: {
            default: null,
            type: cc.ProgressBar
        },
        effectProgressBar: {
            default: null,
            type: cc.ProgressBar
        },
        soundProgressBar: {
            default: null,
            type: cc.ProgressBar
        },
        shakeSlider: {
            default: null,
            type: cc.Slider
        },
        effectSlider: {
            default: null,
            type: cc.Slider
        },
        soundSlider: {
            default: null,
            type: cc.Slider
        },
        telephone: {
            default: null,
            type: cc.Label
        },
        lblBindNewTelephone: {
            default: null,
            type: cc.Label
        },
        newTelephone: {
            default: null,
            type: cc.EditBox
        },
        smsCode: {
            default: null,
            type: cc.EditBox
        },
        bankList: {
            default: null,
            type: cc.ScrollView
        },
        bankItem: {
            default: null,
            type: cc.Node
        },
        provincesList: {
            default: null,
            type: cc.ScrollView
        },
        provincesItem: {
            default: null,
            type: cc.Node
        },
        cityList: {
            default: null,
            type: cc.ScrollView
        },
        cityItem: {
            default: null,
            type: cc.Node
        },
        bindDebitCardNode: {
            default: null,
            type: cc.Node
        },
        bindNewCardNode: {
            default: null,
            type: cc.Node
        },
        accountName: {
            default: null,
            type: cc.EditBox
        },
        accountNumber: {
            default: null,
            type: cc.EditBox
        },
        bankName: {
            default: null,
            type: cc.Label
        },
        cityInfo: {
            default: null,
            type: cc.Label
        },
        bankToggle: {
            default: null,
            type: cc.Toggle
        },
        cityToggle: {
            default: null,
            type: cc.Toggle
        },
        bgCityInfo: {
            default: null,
            type: cc.Sprite
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.audioSetting.active = true
        this.accountPassword.active = false
        this.strongboxPassword.active = false
        this.bindTelephone.active = false
        this.bindDebitCard.active = false
        this.aboutus.active = false

        this.shakeSlider.progress = this.shakeProgressBar.progress = 0.3
        this.effectSlider.progress = this.effectProgressBar.progress = 0.3
        this.soundSlider.progress = this.soundProgressBar.progress = 0.3

        if (App.myUser.telephone) {
            this.telephone.string = App.myUser.telephone
        } else {
            this.telephone.string = "未绑定手机号"
        }

        this.bindDebitCardItem.active = false

        this.bindDebitCardNode.active = true
        this.bindNewCardNode.active = false

        this.bankList.node.active = false
        this.bankItem.active = false
        this.provincesItem.active = false
        this.bgCityInfo.node.active = false
        this.cityItem.active = false

        this.requestCardList()

        let bankList = require("bankList")
        for (let i = 0; i < bankList.length; i++) {
            let bankItem = cc.instantiate(this.bankItem)
            bankItem.active = true
            bankItem.setPosition(cc.v2(-156, 0))
            bankItem.getComponent(cc.Label).string = bankList[i].name

            let button = bankItem.getChildByName("button").getComponent(cc.Button)
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "setting";// 这个是代码文件名
            clickEventHandler.handler = "tapBank";
            clickEventHandler.customEventData = bankList[i].name

            button.clickEvents.push(clickEventHandler);

            this.bankList.content.addChild(bankItem)
        }

        let provinces = require("provinces")
        for (let i = 0; i < provinces.length; i++) {
            let provincesItem = cc.instantiate(this.provincesItem)
            provincesItem.active = true
            provincesItem.setPosition(cc.v2(0, 0))
            provincesItem.getComponent(cc.Label).string = provinces[i].name

            let button = provincesItem.getChildByName("button").getComponent(cc.Button)
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "setting";// 这个是代码文件名
            clickEventHandler.handler = "tapProvinces";
            clickEventHandler.customEventData = provinces[i].name

            button.clickEvents.push(clickEventHandler);

            this.provincesList.content.addChild(provincesItem)
        }

        this.updateCityList("四川省")

        let self = this
        App.on("finishScanCard", function (cardNumber) {
            //self.node.runAction(cc.sequence(cc.delayTime(0), cc.callFunc(function () {
            self.accountNumber.string = cardNumber
            let BankInfoBean = require("BankInfoBean")
            let bankName = new BankInfoBean(cardNumber).getBankName()
            if (bankName != "未知") {
                self.bankName.string = bankName
            }
            //})))

        })
    },

    onCardNumberTextChanged: function (text, editbox, customEventData) {
        let BankInfoBean = require("BankInfoBean")
        let bankName = new BankInfoBean(text).getBankName()
        if (bankName != "未知") {
            this.bankName.string = bankName
        }
    },

    updateCityList(p_provinces) {
        this.cityList.content.removeAllChildren()
        let provinces = require("provinces")
        for (let i = 0; i < provinces.length; i++) {
            if (provinces[i].name == p_provinces) {
                for (let j = 0; j < provinces[i].cities.length; j++) {
                    let cityItem = cc.instantiate(this.cityItem)
                    cityItem.active = true
                    cityItem.setPosition(cc.v2(0, 0))
                    cityItem.getComponent(cc.Label).string = provinces[i].cities[j].name

                    let button = cityItem.getChildByName("button").getComponent(cc.Button)
                    let clickEventHandler = new cc.Component.EventHandler();
                    clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
                    clickEventHandler.component = "setting";// 这个是代码文件名
                    clickEventHandler.handler = "tapCity";
                    clickEventHandler.customEventData = {
                        "provinces": provinces[i].name,
                        "city": provinces[i].cities[j].name
                    }

                    button.clickEvents.push(clickEventHandler);

                    this.cityList.content.addChild(cityItem)
                }
            }
        }
    },

    start() {

    },

    // update (dt) {},

    tapBank(event, customEventData) {
        this.bankName.string = customEventData
        this.bankToggle.getComponent(cc.Toggle).uncheck()
    },

    tapProvinces(event, customEventData) {
        this.updateCityList(customEventData)
    },

    tapCity(event, customEventData) {
        this.cityInfo.string = customEventData.provinces + "-" + customEventData.city
        this.cityToggle.getComponent(cc.Toggle).uncheck()
    },

    updateBindDebitCardList(data) {
        this.bindDebitCardList.content.removeAllChildren()
        for (let i = 0; i < data.bankAccounts.length; i++) {
            let bindDebitCardItem = cc.instantiate(this.bindDebitCardItem)
            bindDebitCardItem.active = true
            bindDebitCardItem.setPosition(cc.v2(0, 0))

            bindDebitCardItem.getChildByName("name").getComponent(cc.Label).string = data.bankAccounts[i].bank

            bindDebitCardItem.getChildByName("cardno").getComponent(cc.Label).string = Tools.getBankCardNumber(data.bankAccounts[i].accountNumber)

            this.bindDebitCardList.content.addChild(bindDebitCardItem)
        }
    },

    changeShake(sender, customEventData) {
        this.shakeProgressBar.progress = sender.progress
    },

    changeEffect(sender, customEventData) {
        this.effectProgressBar.progress = sender.progress
    },

    changeSound(sender, customEventData) {
        this.soundProgressBar.progress = sender.progress
    },

    tapOk(sender, customEventData) {
        switch (customEventData) {
            case "accountPassword":
                {
                    let accountOldPassword = this.accountOldPassword.getComponent(cc.EditBox).string
                    if (accountOldPassword == "") {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("请输入旧密码")
                        return
                    }

                    let accountNewPassword = this.accountNewPassword.getComponent(cc.EditBox).string
                    if (accountNewPassword == "") {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("请输入新密码")
                        return
                    }

                    let accountConfirmPassword = this.accountConfirmPassword.getComponent(cc.EditBox).string
                    if (accountConfirmPassword == "") {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("请再次输入密码")
                        return
                    }

                    if (accountNewPassword != accountConfirmPassword) {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("两次输入的密码不一致")
                        return
                    }

                    /*let accountSecurityCode = this.accountSecurityCode.getComponent(cc.EditBox).string
                    if (accountSecurityCode == "") {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("请输入验证码")
                        return
                    }*/

                    let postData = {
                        "currentPassword": accountOldPassword,
                        "password": accountNewPassword
                    }

                    App.postWithToken("/users/changePassword", postData, function (data) {
                        if (parseInt(data.code) == 200) {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("密码修改成功,请重新登录")
                            App.getWithToken("/auth/logout", function (data) {
                                cc.find('Loading').getComponent("Loading").show()
                                if (parseInt(data.code) == 200) {
                                    cc.director.loadScene("LoginScene")
                                }
                            })
                        } else {
                            cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                        }
                    })
                }
                break
            case "strongboxPassword":
                {
                    if (App.myUser.hasSecureCode == 1) {
                        let strongboxModifyOldPassword = this.strongboxModifyOldPassword.getComponent(cc.EditBox)
                        if (strongboxModifyOldPassword.string == "") {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("当前资金密码不能为空")
                            return
                        }

                        let strongboxModifyNewPassword = this.strongboxModifyNewPassword.getComponent(cc.EditBox)
                        if (strongboxModifyNewPassword.string == "") {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("新资金密码不能为空")
                            return
                        }

                        let strongboxModifyConfirmPassword = this.strongboxModifyConfirmPassword.getComponent(cc.EditBox)
                        if (strongboxModifyConfirmPassword.string == "") {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("两次输入的密码不一致")
                            return
                        }

                        let postData = {
                            "currentSecurityCode": strongboxModifyOldPassword.string,
                            "securityCode": strongboxModifyNewPassword.string
                        }

                        App.postWithToken("/users/changeSecurityCode", postData, function (data) {
                            if (parseInt(data.code) == 200) {
                                cc.find("FadeMsg").getComponent("FadeMsg").show("修改资金密码成功")
                            } else {
                                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                            }
                        })

                    } else {
                        let strongboxFirstNewPassword = this.strongboxFirstNewPassword.getComponent(cc.EditBox)
                        if (strongboxFirstNewPassword.string == "") {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("新资金密码不能为空")
                            return
                        }

                        let strongboxFirstConfirmPassword = this.strongboxFirstConfirmPassword.getComponent(cc.EditBox)
                        if (strongboxFirstConfirmPassword.string == "") {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("资金确认密码不能为空")
                            return
                        }

                        if (strongboxFirstNewPassword.string != strongboxFirstConfirmPassword.string) {
                            cc.find("FadeMsg").getComponent("FadeMsg").show("两次输入的密码不一致")
                            return
                        }

                        let postData = {
                            "securityCode": strongboxFirstNewPassword.string
                        }
                        App.postWithToken("/users/changeSecurityCode", postData, function (data) {
                            if (parseInt(data.code) == 200) {
                                cc.find("FadeMsg").getComponent("FadeMsg").show("设置资金密码成功")
                            } else {
                                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                            }
                        })
                    }
                }
                break
            case "bindTelephone":
                {
                    let self = this
                    let postData = {
                        "telephone": this.newTelephone.string,
                        "validCode": this.smsCode.string
                    }
                    App.postWithToken("/users/validMobile", postData, function (data) {
                        if (parseInt(data.code) == 200) {
                            if (App.myUser.telephone) {
                                cc.find("FadeMsg").getComponent("FadeMsg").show("修改手机号成功")
                            } else {
                                cc.find("FadeMsg").getComponent("FadeMsg").show("设置手机号成功")
                            }
                            App.myUser.telephone = self.newTelephone.string
                            self.telephone.string = self.newTelephone.string
                        } else {
                            cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                        }
                    })
                }
                break
        }
    },

    tapGetSecurityCode(sender, customEventData) {
        let self = this
        cc.loader.load({ url: App.getSecurityCodeUrl(), type: "png" }, function (err, texture) {
            let sp = new cc.SpriteFrame(texture)
            let background = self.accountGetSecurityCode.node.getChildByName("Background")
            background.getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture)
        });
    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    tapSwitchAccount(sender, customEventData) {
        App.getWithToken("/auth/logout", function (data) {
            cc.find('Loading').getComponent("Loading").show()
            if (parseInt(data.code) == 200) {
                cc.director.loadScene("LoginScene")
            }
        })
    },

    tapExitGame(sender, customEventData) {
        App.getWithToken("/auth/logout", function (data) {
            cc.find('Loading').getComponent("Loading").show()
            if (parseInt(data.code) == 200) {
                cc.game.end()
            }
        })
    },

    tapGetSMSCode(sende, customEventData) {
        let postData = {
            "telephone": this.newTelephone.string,
            "type": ""
        }

        App.postWithToken("/auth/sendSMS", postData, function (data) {
            if (parseInt(data.code) == 200) {

            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },

    checkSetting(sender, customEventData) {
        let index = parseInt(customEventData)
        switch (index) {
            case 1:
                {
                    this.audioSetting.active = true
                    this.accountPassword.active = false
                    this.strongboxPassword.active = false
                    this.bindTelephone.active = false
                    this.bindDebitCard.active = false
                    this.aboutus.active = false
                }
                break
            case 2:
                {
                    this.audioSetting.active = false
                    this.accountPassword.active = true
                    this.strongboxPassword.active = false
                    this.bindTelephone.active = false
                    this.bindDebitCard.active = false
                    this.aboutus.active = false
                }
                break
            case 3:
                {
                    this.audioSetting.active = false
                    this.accountPassword.active = false
                    this.strongboxPassword.active = true
                    if (App.myUser.hasSecureCode == 1) {
                        this.strongboxFirstPassword.active = false
                        this.strongboxModifyPassword.active = true
                    } else {
                        this.strongboxFirstPassword.active = true
                        this.strongboxModifyPassword.active = false
                    }
                    this.bindTelephone.active = false
                    this.bindDebitCard.active = false
                    this.aboutus.active = false
                }
                break
            case 4:
                {
                    this.audioSetting.active = false
                    this.accountPassword.active = false
                    this.strongboxPassword.active = false
                    this.bindTelephone.active = true
                    this.bindDebitCard.active = false
                    this.aboutus.active = false
                }
                break
            case 5:
                {
                    this.audioSetting.active = false
                    this.accountPassword.active = false
                    this.strongboxPassword.active = false
                    this.bindTelephone.active = false
                    this.bindDebitCard.active = true
                    this.aboutus.active = false
                }
                break
            case 6:
                {
                    this.audioSetting.active = false
                    this.accountPassword.active = false
                    this.strongboxPassword.active = false
                    this.bindTelephone.active = false
                    this.bindDebitCard.active = false
                    this.aboutus.active = true
                }
        }
    },

    tapAddNewCard(sender, customEventData) {
        this.bindDebitCardNode.active = false
        this.bindNewCardNode.active = true
    },

    tapBackDebitCardList(sender, customEventData) {
        this.bindDebitCardNode.active = true
        this.bindNewCardNode.active = false
    },

    checkBank(sender, customEventData) {
        if (sender.isChecked) {
            sender.node.getChildByName("Background").active = false
            this.bankList.node.active = true
        } else {
            sender.node.getChildByName("Background").active = true
            this.bankList.node.active = false
        }
    },

    checkCity(sender, customEventData) {
        if (sender.isChecked) {
            sender.node.getChildByName("Background").active = false
            this.bgCityInfo.node.active = true
        } else {
            sender.node.getChildByName("Background").active = true
            this.bgCityInfo.node.active = false
        }
    },

    tapImmediatelyBindCard(sender, customEventData) {
        //{"accountName":"钟俊","accountNumber":"6226220639232159","bank":"民生银行","branch":"北京市-北京市"}
        let postData = {
            "accountName": this.accountName.string,
            "accountNumber": this.accountNumber.string,
            "bank": this.bankName.string,
            "branch": this.cityInfo.string
        }
        let self = this
        App.postWithToken("/userBankAccounts/add", postData, function (data) {
            if (parseInt(data.code) == 200) {
                self.bindDebitCardNode.active = true
                self.bindNewCardNode.active = false
                self.requestCardList()
                cc.find("FadeMsg").getComponent("FadeMsg").show("绑定银行卡成功")
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },

    requestCardList() {
        let self = this
        App.getWithToken("/userBankAccounts/list", function (data) {
            if (parseInt(data.code) == 200) {
                self.updateBindDebitCardList(data.data)
            }
        })
    },

    tapScanCard(sender, customEventData) {
        SDKToos.scanCard()
    },

    onDestroy(){
        App.off("finishScanCard")
    }
});
