import App = require('../../script/App');
import itemManager from '../../alter/script/itemManager';
// const  = require("App")
const { ccclass, property } = cc._decorator;

@ccclass
export default class BillManager extends cc.Component {

    @property(cc.Label)
    listTitle: cc.Label[] = [null];

    @property(cc.Node)
    content: cc.Node = null;


    @property(cc.Prefab)
    item: cc.Prefab = null

    @property
    text: string = 'hello';

    config: any[];


    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.config = [
            [{ v: '账户类型', pl: 86 }, { v: '账户详情', pl: 378 }, { v: '时间', pl: 699 }, { v: '盈亏', pl: 883 }],
            [{ v: '投注金额', pl: 86 }, { v: '充值金额', pl: 352 }, { v: '提现金额', pl: 617 }, { v: '盈亏汇总', pl: 883 }],
            [{ v: '投注赛事', pl: 81 }, { v: '下注详情', pl: 333 }, { v: '下注金额', pl: 563 }, { v: '时间', pl: 734 }, { v: '盈亏', pl: 878 }],
            [{ v: '支付类型', pl: 91 }, { v: '订单号', pl: 346 }, { v: '金额', pl: 557 }, { v: '时间', pl: 724 }, { v: '状态', pl: 868 }],
            [{ v: '兑换编号', pl: 92 }, { v: '开户银行', pl: 365 }, { v: '金额', pl: 577 }, { v: '时间', pl: 724 }, { v: '状态', pl: 868 }],
            [{ v: '登录时间', pl: 141 }, { v: 'IP', pl: 525 }, { v: '归属地', pl: 807 }]
        ];
        this.setListTab(this.config[0])
        cc.log(App)
    }

    onClose() {
        this.node.destroy();
    }

    setListTab(v) {
        this.listTitle.forEach((e, i) => {
            if (v[i]) {
                e.node.active = true;
                e.node.x = v[i].pl;
                e.string = v[i].v
            } else {
                e.node.active = false;
            }
        })
    }

    getList(i) {
        let keys = [null, '/users/statis', null, null, null, "/messages/logs"];
        this.content.children.forEach(e => {
            e.destroy()
        });


        switch (i) {
            case '0':

                break;

            case '1':
                App.postTitle(keys[i], this.getListOfDate(), (data) => {
                    this.addItem([data.data], i)
                })
                break;
            case '2':
                App.postWithToken('/esbets/query',{limit:100,startDate:this.getDate},(v)=>{
                    cc.log(v)
                })
                break;
            case '3':

                break;
            case '4':

                break;
            case '5':
                App.getWithToken(keys[i], (data) => {
                    if (parseInt(data.code) == 200) {
                        this.addItem(data.data, i)
                    }
                });
                break;
        }



    }

    add0(v: string) {

        if (v.length != 2) {
            return '0' + v
        } else {
            return v
        }

    }

    getDate(){
        let d = new Date();
        return `${d.getFullYear()}-${this.add0(d.getMonth() + '')}-${this.add0(d.getDate() + '')}`
    }

    getListOfDate() {
        let d = new Date()
        let data = [`date=${d.getFullYear()}-${this.add0(d.getMonth() + '')}-${this.add0(d.getDate() + '')}`]
        cc.log(data)
        return data
    }

    addItem(list: any[], i) {
        list.forEach(e => {
            let node = cc.instantiate(this.item).getComponent(itemManager);
            node.updateData(e, i);
            node.node.parent = this.content;
        })
    }

    onClickTab(e, d) {
        let list = this.config[d];
        this.setListTab(list);

        this.getList(d)
    }

}
