// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        competitionList: {
            default: null,
            type: cc.ScrollView
        },
        competitionItem: {
            default: null,
            type: cc.Prefab
        },
        betPanel: {
            default: null,
            type: cc.Prefab
        },
        datePickPanel: {
            default: null,
            type: cc.Prefab
        },
        friendBattlePanel: {
            default: null,
            type: cc.Prefab
        },
        moreGamePanel: {
            default: null,
            type: cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        for (let i = 0; i < 10; i++) {
            let competitionItem = cc.instantiate(this.competitionItem)
            this.competitionList.content.addChild(competitionItem)

            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "gameplaza";// 这个是代码文件名
            clickEventHandler.handler = "tapCompetition";
            clickEventHandler.customEventData = i;

            let button = competitionItem.getChildByName("button").getComponent(cc.Button);
            button.clickEvents.push(clickEventHandler);
        }
    },

    start() {

    },

    tapHallBack(sender, customEventData) {
        this.node.destroy()
    },

    tapCompetition(sender, customEventData) {
        let betPanel = cc.instantiate(this.betPanel)
        cc.find("Canvas").addChild(betPanel)
    },

    tapDatePick(sender, customEventData) {
        let datePickPanel = cc.instantiate(this.datePickPanel)
        cc.find("Canvas").addChild(datePickPanel)
    },

    tapFriendBattle(sender, customEventData) {
        let friendBattlePanel = cc.instantiate(this.friendBattlePanel)
        cc.find("Canvas").addChild(friendBattlePanel)
    },
    tapMoreGame(sender, customEventData) {
        let moreGamePanel = cc.instantiate(this.moreGamePanel)
        cc.find("Canvas").addChild(moreGamePanel)
    }
    // update (dt) {},
});
