let Tools = function () { }

//是否是闰年
Tools.isLeapYear = function (y) {
    return (y % 4 == 0 && y % 100 != 0) || y % 400 == 0
};

//一个月的天数
Tools.getMonthDays = function (y, m) {
    let t
    if (Tools.isLeapYear(y)) {
        t = [0, 31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    } else {
        t = [0, 31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]
    }
    return t[m]
};

//根据吉姆拉尔森公式计算某年某月某日是星期几
Tools.getWeek = function (y, m, d) {
    if (m == 1 || m == 2) {
        m += 12
        y--
    }
    let w = (d + 2 * m + Math.floor(3 * (m + 1) / 5) + y + Math.floor(y / 4) - Math.floor(y / 100) + Math.floor(y / 400) + 1) % 7
    return w
}

Tools.getWeekStr = function (y, m, d) {
    let w = Tools.getWeek(y, m, d)
    let str = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"]
    return str[w]
}

Tools.todayStr = function () {
    let today = new Date()
    let todayStr = today.getFullYear() + "-" + (today.getMonth() + 1) + "-" + today.getDate()
    return todayStr
};

Tools.timeStr = function () {
    let time = new Date()
    let timeStr = time.getHours() + ":" + time.getMinutes() + ":" + time.getSeconds()
    return timeStr
};

//后一天
Tools.nextDateStr = function () {
    let curDate = new Date()
    var nextDate = new Date(curDate.getTime() + 24 * 60 * 60 * 1000)
    let nextDateStr = nextDate.getFullYear() + "-" + (nextDate.getMonth() + 1) + "-" + nextDate.getDate()
    return nextDateStr
};

Tools.getLowerArab = function (num) {
    let str = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '百', '千', '万']
    if (num >= 0 && num <= 10) {
        return str[num]
    } else if (num == 100) {
        return str[11]
    } else if (num == 1000) {
        return str[12]
    } else if (num = 10000) {
        return str[13]
    }
};

//银行卡卡号星号
Tools.getBankCardNumber = function (number) {
    let str = number
    let reg = /^(\d{4})\d+(\d{4})$/
    str = str.replace(reg, "$1 **** **** $2")
    return str
};

Tools.findChildByName = function (root, name) {
    let child = root.getChildByName(name)
    if (child) {
        return child
    }

    let children = root.children
    for (let i = 0; i < children.length; i++) {
        let rightone = Tools.findChildByName(children[i],name)
        if(rightone){
            return rightone
        }
    }

    return null
};

Tools.getRandomInt =function(min, max){
    return Math.floor(Math.random() * (max - min + 1)) + min
}

Tools.GetGameStateStr = function(state){
    let stateStr = ["竞猜中", "比赛中", "已取消", "已暂停", "已结束", "已结算"]
    return stateStr[state]
}

module.exports = Tools