cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        mask: {
            default: null,
            type: cc.Sprite
        },
        yearPageView: {
            default: null,
            type: cc.PageView
        },
        yearItem: {
            default: null,
            type: cc.Prefab
        },
        monthPageView: {
            default: null,
            type: cc.PageView
        },
        monthItem: {
            default: null,
            type: cc.Prefab
        },
        dayPageView: {
            default: null,
            type: cc.PageView
        },
        dayItem: {
            default: null,
            type: cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if(event.stopProgagation != undefined){
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if(event.stopProgagation != undefined){
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if(event.stopProgagation != undefined){
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if(event.stopProgagation != undefined){
                event.stopProgagation()
            }
        })

        let self = this
        this.mask.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            self.node.destroy()
        })

        for (let i = 0; i < 10; i++) {
            this.yearPageView.content.addChild(cc.instantiate(this.yearItem))
        }
        this.yearPageView.getComponent(cc.PageView).scrollToTop()

        for (let i = 0; i < 12; i++) {
            let monthItem = cc.instantiate(this.monthItem)
            monthItem.getComponent(cc.Label).string = String(i + 1)
            this.monthPageView.content.addChild(monthItem)
        }
        this.monthPageView.getComponent(cc.PageView).scrollToTop()

        for (let i = 0; i < 10; i++) {
            this.dayPageView.content.addChild(cc.instantiate(this.dayItem))
        }
        this.dayPageView.getComponent(cc.PageView).scrollToTop()
    },

    start() {

    },

    pageYear(sender, customEventData) {
        //cc.log(sender.getCurrentPageIndex())
    }

    // update (dt) {},
});
