    updateBetOrderSum() {
        let children = this.betOrderList.content.children
        let length = children.length
        let amountSum = 0
        let profitableSum = 0
        for (let i = 0; i < length; i++) {
            let amountEditBox = children[i].getChildByName("amountEditBox").getComponent(cc.EditBox)
            let str = amountEditBox.string
            let amount = Boolean(parseInt(str)).valueOf() == false ? 0 : parseInt(str)
            amountSum += amount
            profitableSum += (amount * children[i].betRate)
        }
        this.betOrderSum.string = cc.js.formatStr("<color=#999999>共</color><color=#249ED2>%s</color><color=#999999>注，合计:</color><color=#249ED2>%s</color>   <color=#999999>可赢额: %s</color>",
            length, amountSum, profitableSum.toFixed(2))
    },

