cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        progressBar: {
            default: null,
            type: cc.ProgressBar
        },
        status: {
            default: null,
            type: cc.Label
        },
        percent: {
            default: null,
            type: cc.Label
        },
        updateServerUrl: "http://192.168.0.82:8080/heroesports",
        user: "opener",
        updateDir: "update",
        updateTempDir: "updateTemp"
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        cc.log("onLoad")
        cc.debug.setDisplayStats(false)

        cc.view.setOrientation(cc.macro.ORIENTATION_LANDSCAPE)

        let self = this

        this.initUpdate()

        //向服务器请求版本
        let xhr = new XMLHttpRequest()
        let url = this.updateServerUrl + "/update/appversion?user=" + this.user
        xhr.open("GET", url)

        xhr.send()

        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    let jsonData = null
                    try {
                        jsonData = JSON.parse(xhr.responseText)
                    } catch (e) {
                        cc.log('parsing error: ', xhr.responseText)
                    }
                    if (parseInt(jsonData.code) == 200) {
                        let major = jsonData.data.major
                        let minor = jsonData.data.minor
                        self.updateUrl = self.updateServerUrl + jsonData.data.updateUrl
                        self.forceUpdateUrl = jsonData.data.forceUpdateUrl
                        self.pakcageJsonUrl = jsonData.data.pakcageJsonUrl
                        self.checkNeedUpate(major, minor)
                    } else {

                    }
                }
            }
        }

        // let src = "res/raw-assets/61/61abefc8-5ba0-4545-bf9e-d12ca3cb1158.jpg"
        // let index = src.lastIndexOf("/")
        // cc.log(src.substr(0,index))
    },

    initUpdate() {
        let writeablePath = jsb.fileUtils.getWritablePath()
        this.updateDir = writeablePath + this.updateDir
        if (jsb.fileUtils.isDirectoryExist(this.updateDir) == false) {
            //将包内资源复制到更新目录
            let packageJson = jsb.fileUtils.getStringFromFile("res/package.json")
            let jsonData = JSON.parse(packageJson)
            jsb.fileUtils.createDirectory(this.updateDir)
            for (let filePath in jsonData.files) {
                this.copyAssetFile(filePath)
            }
            //将包内的res/package.json复制到更新目录
            let fileData = jsb.fileUtils.getDataFromFile("res/package.json")
            jsb.fileUtils.writeDataToFile(fileData, this.updateDir + "/" + "res/package.json")
        }

        this.updateTempDir = writeablePath + this.updateTempDir
        if (jsb.fileUtils.isDirectoryExist(this.updateTempDir) == false) {
            jsb.fileUtils.createDirectory(this.updateTempDir)
        }
    },

    checkNeedUpate(remoteMajor, remoteMinor) {
        let packageJson = jsb.fileUtils.getStringFromFile(this.updateDir + "/" + "res/package.json")
        let jsonData = JSON.parse(packageJson)
        let localMajor = jsonData.major
        let localMinor = jsonData.minor
        cc.log("本地版本号:", localMajor, localMinor)
        cc.log("服务器版本号:", remoteMajor, remoteMinor)
        if (localMajor < remoteMajor) {
            //强更
            cc.sys.openURL(this.forceUpdateUrl)
        }
        else if (localMajor > remoteMajor) {
            //无需更新
            cc.director.loadScene("LoginScene")
        }
        else if (localMajor = remoteMajor) {
            if (localMinor < remoteMinor) {
                //小版本更新
                this.fetchResourceList()
            } else {
                //无需更新
                cc.director.loadScene("LoginScene")
            }
        }
    },

    copyAssetFile(filePath) {
        let dotIndex = filePath.lastIndexOf("/")
        let tempDir = filePath.substr(0, dotIndex)
        let dir = this.updateDir + "/" + tempDir
        if (jsb.fileUtils.isDirectoryExist(dir) == false) {
            jsb.fileUtils.createDirectory(dir)
        }
        let fileData = jsb.fileUtils.getDataFromFile(filePath)
        jsb.fileUtils.writeDataToFile(fileData, this.updateDir + "/" + filePath)
    },

    fetchResourceList() {
        let self = this
        let path = this.updateTempDir + "/package.json"
        let url = this.updateServerUrl + this.pakcageJsonUrl
        cc.log(path, url)
        mycc.HttpHelper.getInstance().fetch(url, path, function (ok, filename) {
            if (ok) {
                self.compareFiles()
            }
        })
    },

    compareFiles() {
        let localPackageJson = jsb.fileUtils.getStringFromFile(this.updateDir + "/" + "res/package.json")
        let localJsonData = JSON.parse(localPackageJson)
        let localFileList = localJsonData.files

        let remotePackageJson = jsb.fileUtils.getStringFromFile(this.updateTempDir + "/package.json")
        let remoteJsonData = JSON.parse(remotePackageJson)
        let remoteFileList = remoteJsonData.files

        this.needRemoveFiles = new Array
        //本地存在，服务器端不存在的文件，要删除
        for (let file in localFileList) {
            if (!remoteFileList[file]) {
                cc.log("need remove: ", file)
                this.needRemoveFiles.push(file)
            }
        }
        this.needFetchFiles = new Array
        //服务器和客户单md5码不一致，包括了服务器端存在，客户端不存在的文件，要下载
        for (let file in remoteFileList) {
            if (localFileList[file] != remoteFileList[file]) {
                cc.log("need fetch: ", file)
                this.needFetchFiles.push(file)
            }
        }

        if (this.needFetchFiles.length > 0) {
            this.fetchIndex = 0
            this.fetchOne()
        }
        else{
            cc.director.loadScene("LoginScene")
        }
    },

    fetchOne() {
        let self = this
        let filePath = this.needFetchFiles[this.fetchIndex]
        let url = this.updateUrl + "/" + filePath
        let path = this.updateTempDir + "/" + filePath
        cc.log(path, url, self.fetchIndex)
        let dotIndex = filePath.lastIndexOf("/")
        let tempDir = filePath.substr(0, dotIndex)
        let dir = this.updateTempDir + "/" + tempDir
        if (jsb.fileUtils.isDirectoryExist(dir) == false) {
            jsb.fileUtils.createDirectory(dir)
        }
        mycc.HttpHelper.getInstance().fetch(url, path, function (ok, filename) {
            if (ok) {
                if (self.fetchIndex < self.needFetchFiles.length - 1) {
                    self.fetchIndex = self.fetchIndex + 1
                    self.fetchOne()
                } else {
                    //下载完毕
                    self.tempToUpdate()
                }
            } else {
                cc.log("download error: ", url)
            }
        })
    },

    tempToUpdate() {
        //将临时目录中的文件移动到更新目录
        for (let i = 0; i < this.needFetchFiles.length; i++) {
            let filePath = this.needFetchFiles[i]
            let dotIndex = filePath.lastIndexOf("/")
            let tempDir = filePath.substr(0, dotIndex)
            let dir = this.updateDir + "/" + tempDir
            if (jsb.fileUtils.isDirectoryExist(dir) == false) {
                jsb.fileUtils.createDirectory(dir)
            }
            let srcFile = this.updateTempDir + "/" + filePath
            let dstFile = this.updateDir + "/" + filePath
            jsb.fileUtils.renameFile(srcFile, dstFile)
        }

        //删除本地多于的文件
        for (let i = 0; i < this.needRemoveFiles; i++) {
            let file = this.updateDir + "/" + this.needRemoveFiles[i]
            jsb.fileUtils.removeFile(file)
        }

        //覆盖package.json文件
        let srcPackageJson = this.updateTempDir + "/package.json"
        let dstPackageJson = this.updateDir + "/" + "res/package.json"
        jsb.fileUtils.renameFile(srcPackageJson, dstPackageJson)

        //更新完毕，重启游戏
        cc.log("更新完毕，重启游戏")
        cc.director.loadScene("LoginScene")
    },

    start() {

    },

    // update (dt) {},
});
