let App = require("App")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },

        newbieTask: {
            default: null,
            type: cc.Node
        },
        newbieTaskList: {
            default: null,
            type: cc.ScrollView
        },
        taskItem: {
            default: null,
            type: cc.Node
        },
        todayTask: {
            default: null,
            type: cc.Node
        },
        todayTaskList: {
            default: null,
            type: cc.ScrollView
        },
        rechargeTask: {
            default: null,
            type: cc.Node
        },
        rechargeTaskList: {
            default: null,
            type: cc.ScrollView
        },
        taskLog: {
            default: null,
            type: cc.Node
        },
        taskLogList: {
            default: null,
            type: cc.ScrollView
        },
        taskLogItem: {
            default: null,
            type: cc.Prefab
        },
        registerSendGold: {
            default: null,
            type: cc.Node
        },
        allPeopleAgent: {
            default: null,
            type: cc.Node
        },
        shareDialog: {
            default: null,
            type: cc.Prefab
        },
        moreActivity: {
            default: null,
            type: cc.Node
        },
        activityList: {
            default: null,
            type: cc.ScrollView
        },
        activityDetail: {
            default: null,
            type: cc.Node
        },
        moreActivityItem: {
            default: null,
            type: cc.Node
        },
        webview: {
            default: null,
            type: cc.WebView
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.newbieTask.active = true
        this.todayTask.active = false
        this.taskLog.active = false
        this.rechargeTask.active = false
        this.registerSendGold.active = false
        this.allPeopleAgent.active = false
        this.moreActivity.active = false
        this.activityDetail.active = false
        this.moreActivityItem.active = false

        this.taskItem.active = false

        this.requestTaskList()

        this.requestMoreActivity()
    },

    start() {

    },

    // update (dt) {},

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    checkActivity(sender, customEventData) {
        let index = parseInt(customEventData)
        switch (index) {
            case 1:
                {
                    this.newbieTask.active = true
                    this.todayTask.active = false
                    this.taskLog.active = false
                    this.rechargeTask.active = false
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = false
                }
                break
            case 2:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = true
                    this.taskLog.active = false
                    this.rechargeTask.active = false
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = false
                }
                break
            case 3:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = false
                    this.taskLog.active = true
                    this.requestTaskLogList()
                    this.rechargeTask.active = false
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = false
                }
                break
            case 4:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = false
                    this.taskLog.active = false
                    this.rechargeTask.active = true
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = false
                }
                break
            case 5:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = false
                    this.taskLog.active = false
                    this.rechargeTask.active = false
                    this.registerSendGold.active = true
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = false
                }
                break
            case 6:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = false
                    this.taskLog.active = false
                    this.rechargeTask.active = false
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = true
                    this.moreActivity.active = false

                    let shareDialog = cc.instantiate(this.shareDialog)
                    cc.find("Canvas").addChild(shareDialog)
                }
                break
            case 7:
                {
                    this.newbieTask.active = false
                    this.todayTask.active = false
                    this.taskLog.active = false
                    this.rechargeTask.active = false
                    this.registerSendGold.active = false
                    this.allPeopleAgent.active = false
                    this.moreActivity.active = true
                }
                break
        }
    },
    updateTaskList(listRoot, data) {
        for (let i = 0; i < data.length; i++) {
            let taskItem = cc.instantiate(this.taskItem)
            taskItem.active = true
            taskItem.setPosition(cc.v2(0, 0))

            if (data[i].type == 3) {
                taskItem.getChildByName("name").getComponent(cc.RichText).string = data[i].name
            } else {
                let nameStr = cc.js.formatStr("<color=#BCD3F7>%s</c><color=#BCD3F7>", data[i].name)
                taskItem.getChildByName("name").getComponent(cc.RichText).string = nameStr
            }

            let rewardStr
            let rewardIconStr
            if (data[i].type == 1) {
                rewardStr = cc.js.formatStr("<color=#BCD3F7>任务奖励:  </c><color=#BCD3F7>经验</c><color=#0000FF>%s</color><color=#BCD3F7>点</color>", data[i].amount)
                rewardIconStr = "icon_reward_vip"
            }
            else if (data[i].type == 2) {
                rewardStr = cc.js.formatStr("<color=#BCD3F7>任务奖励:  </c><color=#BCD3F7>金币</c><color=#0000FF>%s</color><color=#BCD3F7>个</color>", data[i].amount)
                rewardIconStr = "icon_reward_gold"
            } else if (data[i].type == 3) {
                rewardStr = cc.js.formatStr("<color=#BCD3F7>任务奖励:  </c><color=#BCD3F7>金币</c><color=#0000FF>%s</color><color=#BCD3F7>个</color>", data[i].amount)
                rewardIconStr = "icon_reward_gold"
            }
            taskItem.getChildByName("reward").getComponent(cc.RichText).string = rewardStr
            cc.loader.loadRes(rewardIconStr, cc.SpriteFrame, function (err, spriteFrame) {
                if (err) {
                    cc.log(err)
                    return
                }
                taskItem.getChildByName("bg_reward").getChildByName("icon_reward").getComponent(cc.Sprite).spriteFrame = spriteFrame
            })

            let state = taskItem.getChildByName("state")
            let stateButton = state.getComponent(cc.Button)
            let stateLabel = state.getChildByName("Background").getChildByName("Label").getComponent(cc.Label)
            if (data[i].state == null) {
                stateLabel.string = "去完成"
            } else if (data[i].state == 1) {
                stateLabel.string = "领取奖励"

                var clickEventHandler = new cc.Component.EventHandler();
                clickEventHandler.target = this.node;
                clickEventHandler.component = "activity";
                clickEventHandler.handler = "tapGetReward";
                clickEventHandler.customEventData = {
                    "taskId": data[i].taskId,
                    "sender": taskItem
                };

                stateButton.clickEvents.push(clickEventHandler);
            } else if (data[i].state == 2) {
                stateButton.interactable = false
                stateLabel.string = "已完成"
            }

            listRoot.content.addChild(taskItem)
        }
    },
    tapGetReward(eventTouch, customEventData) {
        let self = this
        let postData = [
            "taskId=" + customEventData.taskId
        ]
        App.postTitle("/tasks/apply", postData, function (data) {
            if (parseInt(data.code) == 200) {
                cc.find("FadeMsg").getComponent("FadeMsg").show("领取成功")
                let progress = customEventData.sender.getChildByName("progress").getComponent(cc.Label)
                let progressBar = customEventData.sender.getChildByName("progressBar").getComponent(cc.ProgressBar)
                let state = customEventData.sender.getChildByName("state")
                let stateButton = state.getComponent(cc.Button)
                let stateLabel = state.getChildByName("Background").getChildByName("Label").getComponent(cc.Label)
                progress.string = "100%"
                progressBar.progress = 1
                stateButton.interactable = false
                stateLabel.string = "已完成"
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },

    requestTaskList() {
        let self = this
        App.getWithToken("/tasks/list", function (data) {
            if (200 == parseInt(data.code)) {
                self.updateTaskList(self.newbieTaskList, data.data.fixedItems)
                self.updateTaskList(self.todayTaskList, data.data.todayItems)
                if (data.data.rechargeItems == undefined) {
                    data.data.rechargeItems = [
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>完成</c><color=#0000FF>首次</color><color=#BCD3F7>充值</color>",
                            "amount": 1,
                            "state": 1,
                            "categoryId": 4,
                            "taskId": 218,
                            "link": null
                        },
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>累计充值达</c><color=#0000FF>10</color><color=#BCD3F7>元</color>",
                            "amount": 2,
                            "state": 1,
                            "categoryId": 5,
                            "taskId": 221,
                            "link": "/sign"
                        },
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>累计充值达</c><color=#0000FF>50</color><color=#BCD3F7>元</color>",
                            "amount": 5,
                            "state": null,
                            "categoryId": 6,
                            "taskId": null,
                            "link": "/account/deposit/?category=user"
                        },
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>累计充值达</c><color=#0000FF>100</color><color=#BCD3F7>元</color>",
                            "amount": 10,
                            "state": null,
                            "categoryId": 7,
                            "taskId": null,
                            "link": "/account/deposit/withdrawal?category=user"
                        },
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>累计充值达</c><color=#0000FF>200</color><color=#BCD3F7>元</color>",
                            "amount": 20,
                            "state": null,
                            "categoryId": 7,
                            "taskId": null,
                            "link": "/account/deposit/withdrawal?category=user"
                        },
                        {
                            "type": 3,
                            "name": "<color=#BCD3F7>累计充值达</c><color=#0000FF>500</color><color=#BCD3F7>元</color>",
                            "amount": 50,
                            "state": null,
                            "categoryId": 7,
                            "taskId": null,
                            "link": "/account/deposit/withdrawal?category=user"
                        }
                    ]
                }
                self.updateTaskList(self.rechargeTaskList, data.data.rechargeItems)
            }
        })
    },
    requestTaskLogList() {
        let self = this
        App.getWithToken("/tasks/query", function (data) {
            if (200 == parseInt(data.code)) {
                for (let i = 0; i < data.data.content.length; i++) {
                    let taskLogItem = cc.instantiate(self.taskLogItem)

                    let time = taskLogItem.getChildByName("time").getComponent(cc.Label)
                    let reward = taskLogItem.getChildByName("reward").getComponent(cc.Label)
                    let name = taskLogItem.getChildByName("name").getComponent(cc.Label)

                    time.string = data.data.content[i].createDate

                    if (data.data.content[i].category.type == 2) {
                        reward.string = data.data.content[i].category.amount + "经验"
                    }

                    name.string = data.data.content[i].category.name

                    self.taskLogList.content.addChild(taskLogItem)
                }
            }
        })
    },

    requestMoreActivity() {
        let self = this
        App.get("/promotionCategories/visible", function (data) {
            if (200 == parseInt(data.code)) {
                self.updateMoreActivity(data.data)
            }
        })
    },

    updateMoreActivity(data) {
        cc.log(data)
        for (let i = 0; i < data.length; i++) {
            let moreActivityItem = cc.instantiate(this.moreActivityItem)
            moreActivityItem.active = true
            moreActivityItem.setPosition(cc.v2(0, 0))

            moreActivityItem.getChildByName("name").getComponent(cc.Label).string = data[i].name
            if (data[i].img) {
                cc.loader.load({ url: data[i].img}, function (err, texture) {
                    if (err) {
                        return
                    }

                    let sp = new cc.SpriteFrame(texture)
                    moreActivityItem.getChildByName("img").getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture)

                });
            }

            let clickEventHandler = new cc.Component.EventHandler()
            clickEventHandler.target = this.node
            clickEventHandler.component = "activity"
            clickEventHandler.handler = "tapViewDetail"
            clickEventHandler.customEventData = data[i].id

            let button = moreActivityItem.getChildByName("button").getComponent(cc.Button)
            button.clickEvents.push(clickEventHandler)

            this.activityList.content.addChild(moreActivityItem)
        }
    },

    tapViewDetail(evnet, customEventData) {
        cc.log(customEventData)
        this.activityDetail.active = true
        this.activityList.node.active = false
        this.webview.url = "http://192.168.0.55" + "/api/promotionCategories/detail?id=" + customEventData
    },

    tapCloseActivityDetail(sender, customEventData) {
        this.activityDetail.active = false
        this.activityList.node.active = true
    }
});
