

cc.Class({
    extends: cc.Component,

    properties: {
        
        content: cc.Node,
        spacingY: 466,
        curSelect: cc.Node
    },


    setGroup() {

        if(this.group) return;
        this.group = [];
        for(var i = 0; i < this.content.childrenCount; i++) {

            this.group.push(this.content.children[i]);
            this.content.children[i].x = i * (this.spacingY - 200) + 590;
            this.content.children[i].groupIndex = i;
            this.content.children[i].on('touchend', this.click, this);
        }
        this.content.x = -590;
        this.content.width = this.content.childrenCount * this.spacingY + 100;
    },


    setCurSelect(n) {

        this.curSelect = n;
        this.setChilColor(n, cc.color('#FFFFFF'));
    },


    setOtherList(n) {

        this.setChilColor(n, cc.color('#838383'));
    },


    setItem() {

        var children = this.group || this.content.children;
        var i, n, sc;
        var t = 1000;

        for(i = 0; i < children.length; i++) {

            if(Math.abs(children[i].x + this.content.x) < t) n = i;
            
            t = children[i].x + this.content.x;
            
            if(t >= -400 && t <= 400) {

                sc = 1.05 - Math.abs(t) / 400 * 0.25;
                children[i].scale = sc;
            }
            else {
                children[i].scale = 0.8;
            }

            t = Math.abs(children[i].x + this.content.x);
        }


        for(i = 0; i < children.length; i++) {

            if(i == n) this.setCurSelect(children[n]);
            else this.setOtherList(children[i]);
            children[i].zIndex = i;
        }

        for(i = n; i < children.length; i++) {

            children[i].zIndex = n - (i - n);
        }
    },


    scrolling() {

        this.node.emit('scrolling');
    },


    toItem(n) {

        this.content.runAction(cc.sequence(
            cc.moveTo(0.2, -this.group[n].x, this.content.y),
            cc.callFunc(()=> {

                this.unschedule(this.scrolling);
                this.setItem()
            })
        ))

        this.schedule(this.scrolling, 0.05)
    },


    init() {

        this._curSelect = 0;
        this.setGroup();
        this.setItem();

        this.node.on('scrolling', (e)=> {

            this.setItem();
        })
    },


    click(e) {

        if(this.curSelect.groupIndex == e.currentTarget.groupIndex) e.currentTarget.emit('enter', {
            target: this.curSelect,
            index: this.curSelect.groupIndex
        });
        else this.toItem(e.currentTarget.groupIndex);
    },


    setChilColor(n, color) {

        n.children[0].color = color;
        n.children[1].color = color;
        n.children[2].color = color;
        n.children[3].color = color;
        n.children[3].children[0].color = color;
    }


});
