let App = require("App")
let Tools = require("Tools")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        accessRecordList: {
            default: null,
            type: cc.ScrollView
        },
        accessRecordItem: {
            default: null,
            type: cc.Node
        },
        saveOrDraw: {
            default: null,
            type: cc.Node
        },
        accessRecord: {
            default: null,
            type: cc.Node
        },
        operateTitle: {
            default: null,
            type: cc.Label
        },
        operateButtonBg: {
            default: null,
            type: cc.Sprite
        },
        number: {
            default: null,
            type: cc.EditBox
        },
        toggle: 1,
        openBankPanel: {
            default: null,
            type: cc.Prefab
        },
        platformBalance:{
            default: null,
            type: cc.Label
        },
        balance:{
            default: null,
            type: cc.Label
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.accessRecord.active = false
        this.saveOrDraw.active = true

        this.accessRecordItem.active = false
        //this.requestAccessRecordList()

        let getData = [
            "platformId="+App.esportPlatformId,
        ]
        let self = this
        App.getWithTokenData("/users/getPlatformBalance",getData,function(data){
            if(parseInt(data.code) == 200){
                self.platformBalance.string = data.data
            }
        })

        App.getWithToken("/users/getUserBalance",function(data){
            if(parseInt(data.code) == 200){
                self.balance.string = data.data
            }
        })
    },

    start() {

    },

    requestAccessRecordList(){
        let postData = {
            "endDate": Tools.todayStr(),
            "limit":100,
            "offset": 0,
            "platformId": App.esportPlatformId,
            "startDate":Tools.todayStr(),
        }

        //cc.log("postData = ",postData)
        let self = this
        App.postWithToken("/credits/query", postData, function (data) {
            //cc.log(data)
            if (parseInt(data.code) == 200) {
                self.updateAccessRecordList(data.data.content)
            } else {
                cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
            }
        })
    },

    updateAccessRecordList(data){
        this.accessRecordList.content.removeAllChildren()
        for (let i = 0; i < data.length; i++) {
            let accessRecordItem = cc.instantiate(this.accessRecordItem)
            accessRecordItem.active = true
            accessRecordItem.setPosition(cc.v2(0,0))

            accessRecordItem.getChildByName("time").getComponent(cc.Label).string = data[i].createDate
            if(data[i].type == 1){
                accessRecordItem.getChildByName("exchangeType").getComponent(cc.Label).string = "存入金币"
            }else if(data[i].type ==2){
                accessRecordItem.getChildByName("exchangeType").getComponent(cc.Label).string = "取出金币"
            }

            let amountStr = cc.js.formatStr("<color=#FAB622>%s</c><color=#BCD3F7>(单位:个)</color>",data[i].amount)
            accessRecordItem.getChildByName("amount").getComponent(cc.RichText).string = amountStr
            let walletAmount = cc.js.formatStr("<color=#FAB622>%s</c><color=#BCD3F7>(单位:个)</color>",data[i].creditAfter)
            accessRecordItem.getChildByName("walletAmount").getComponent(cc.RichText).string = amountStr


            this.accessRecordList.content.addChild(accessRecordItem)
        }
    },

    checkOperate(sender, customEventData) {
        this.toggle = customEventData
        let self = this
        if (sender.isChecked) {
            if (customEventData == 1) {
                this.saveOrDraw.active = true
                this.accessRecord.active = false
                cc.loader.loadRes("btn_save_money", cc.SpriteFrame, function (err, spriteFrame) {
                    if (err) {
                        return
                    }
                    self.operateButtonBg.spriteFrame = spriteFrame
                })

                self.operateTitle.string = "请输入您要存入的金币"
            }
            else if (customEventData == 2) {
                this.saveOrDraw.active = true
                this.accessRecord.active = false
                cc.loader.loadRes("btn_draw_money", cc.SpriteFrame, function (err, spriteFrame) {
                    if (err) {
                        return
                    }
                    self.operateButtonBg.spriteFrame = spriteFrame
                })

                self.operateTitle.string = "请输入您要取出的金币"
            }
            else if (customEventData == 3) {
                this.saveOrDraw.active = false
                this.accessRecord.active = true

                this.requestAccessRecordList()
            }
        }
    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    tapOperate(sender, customEventData) {
        if (!(App.myUser.hasSecureCode)) {
            let openBankPanel = cc.instantiate(this.openBankPanel)
            cc.find("Canvas").addChild(openBankPanel)
            return
        }

        let str = this.number.string
        let amount = Boolean(parseInt(str)).valueOf() == false ? 0 : parseInt(str)
        //存入
        if (this.toggle == 1) {
            let postData = {
                "amount": amount,
                "platformId": App.esportPlatformId
            }
            App.postWithToken('/credits/in', postData, function (data) {
                if (parseInt(data.code) == 200) {

                } else {
                    cc.log(data)
                    cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                }
            })
            //取出
        } else if (this.toggle == 2) {
            let postData = {
                "amount": amount,
                "platformId": App.esportPlatformId
            }
            App.postWithToken('/credits/out', postData, function (data) {
                if (parseInt(data.code) == 200) {

                } else {
                    cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                }
            })
        }
    },

    tapNumber(sender, customEventData) {
        this.number.string = String(customEventData)
    }
    // update (dt) {},
});
