//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
const App = require("App")
let SDKTools = require('SDKTools')
cc.Class({
    extends: cc.Component,

    properties: {
        userid: {
            default: null,
            type: cc.Label
        },

        userName: {
            default: null,
            type: cc.EditBox
        },

        birthday: {
            default: null,
            type: cc.Label
        },

        userDynamic: {
            default: null,
            type: cc.EditBox
        },

        sex: {
            default: null,
            type: cc.ToggleContainer
        },

        headImage: {
            default: null,
            type: cc.Sprite
        },

        gold: {
            default: null,
            type: cc.Label
        },
        // _golds

        shareDialog: {
            default: null,
            type: cc.Prefab
        },
        recharge: {
            default: null,
            type: cc.Prefab
        },
        conversion: {
            default: null,
            type: cc.Prefab
        },
        setting: {
            default: null,
            type: cc.Prefab
        },

        billDialog: {
            default: null,
            type: cc.Prefab
        },


        noticeDialog: {
            default: null,
            type: cc.Prefab
        },
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        cc.log(App)
        this.updataView()
    },

    updataView() {
        if (!App.myUser) return
        this.userid.string = `ID:${App.myUser.id}`;
        this.userName.string = App.myUser.username;
        // this.birthday.string = App.myUser.id;
        // this.userDynamic.string = App.myUser.id;

        this.sex.node.children[0].getComponent(cc.Toggle).isChecked = App.myUser.sex;
        this.sex.node.children[1].getComponent(cc.Toggle).isChecked = !App.myUser.sex;

        this.updateAvatar()

        App.getWithToken("/users/getUserBalance", function (data) {
            if (parseInt(data.code) == 200) {
                App.myUser.setup(data.data)
                self.gold.string = data.data
            }
        })

        let self = this
        App.on("finishSelectImage", function (savePath, imageWidth, imageHeight) {
            cc.loader.load({ url: savePath, type: "binary" }, function (err, result) {
                App.postImage("/users/uploadAvatarStream", result, function (data) {
                    if (parseInt(data.code) == 200) {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("上传头像成功，请稍后查看")
                        cc.find("Canvas").getComponent("HallScene").updateAvatar()
                        self.updateAvatar()
                    } else {
                        cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                    }
                })
            })
        })

        // this.gold.string=9;
    },

    updateAvatar() {
        let self = this;
        cc.loader.load({ url: App.getHeadImageByUserId(App.myUser.id), type: "png" }, function (err, texture) {
            if (err) {
                return
            }
            let sp = new cc.SpriteFrame(texture)

            self.headImage.spriteFrame = new cc.SpriteFrame(texture)
        });
    },


    // start () {
    //     cc.log(App)
    // },

    onSwitchHeadImage(sender, customEventData) {
        if (cc.sys.isNative) {
            SDKTools.selectAlbumImage()
        }
    },

    onClose() {
        this.node.destroy()
    },

    onClickStore() {
        let postData = {

            "birthdate": '',
            "email": '',
            "nickname": '',
            "profile": '',
            "sex": this.sex.node.children[0].getComponent(cc.Toggle).isChecked ? 1 : 0,
            "truename": this.userName.string
        }

        cc.log(postData)
        App.post("/users/update", postData, function (data) {
            cc.log(data)
        });
    },

    onClickShare() {
        let shareDialog = cc.instantiate(this.shareDialog)
        cc.find("Canvas").addChild(shareDialog)
    },

    onClickRecharge() {
        let recharge = cc.instantiate(this.recharge)
        cc.find("Canvas").addChild(recharge)
    },

    onClickConversion() {
        let conversion = cc.instantiate(this.conversion)
        cc.find("Canvas").addChild(conversion)
    },

    onClickSetting() {
        let setting = cc.instantiate(this.setting)
        cc.find("Canvas").addChild(setting)
    },

    onClickBill() {
        let billDialog = cc.instantiate(this.billDialog)
        cc.find("Canvas").addChild(billDialog)
    },

    onClickNotice() {
        let noticeDialog = cc.instantiate(this.noticeDialog)
        cc.find("Canvas").addChild(noticeDialog)
    },

    // update (dt) {},

    onDestroy() {
        if (this.updateHeadImageFunc) {
            this.updateHeadImageComponent.unschedule(this.updateHeadImageFunc)
        }

        App.off("finishSelectImage")
    }
});
