let MyUser = require("MyUser")

let App = new cc.EventTarget()
App.myUser = MyUser

App.wsurl = "ws://192.168.0.55:8099/websocket"
App.apiurl = "http://192.168.0.55:8088"
//App.wsurl = "ws://192.168.0.82:8080/heroesports2/ws"
//App.apiurl = "http://192.168.0.82:8080/heroesports"
App.token = ""
App.requestToken = ""
App.esportPlatformId = -1
App.xhrArray = []

App.connectServer = function () {
    App.ws = new WebSocket(App.wsurl)
    App.ws.onopen = function (evt) {
        App.emit("connectServerSuccess")
        //App.ws.send("ok")
    };

    App.ws.onmessage = function (evt) {
        //cc.log(evt.data)
        let jsonData = null
        try {
            jsonData = JSON.parse(evt.data)
        } catch (e) {
            cc.log('parsing error: ', evt.data)
        }
        if (jsonData != null) {
            App.emit('channel.' + jsonData.channel, jsonData.data)
        }
    };

    App.ws.onclose = function (evt) {
        App.emit("networkClosed")
    };

    App.ws.onerror = function (evt) {
        App.emit("networkError")
    }
};

App.sendMsg = function (msg) {
    App.ws.send(msg)
};

App.do_onreadystatechange = function (xhr, callback) {
    if (xhr.readyState === 4) {
        if (xhr.status === 200) {

            let indexOf = -1
            for (let i = 0; i < App.xhrArray.length; i++) {
                if (App.xhrArray[i] == xhr) {
                    indexOf = i
                    break
                }
            }
            //cc.log(indexOf,App.xhrArray)
            if (indexOf > -1) {
                App.xhrArray.splice(indexOf, 1)
            }

            if (callback) {
                let jsonData = null
                try {
                    jsonData = JSON.parse(xhr.responseText)
                } catch (e) {
                    cc.log('parsing error: ', xhr.responseText)
                }
                if (jsonData != null) {
                    callback(jsonData)
                }
            }
        }
    }
};

App.clearAllHttp = function(){
    for (let i = 0; i < App.xhrArray.length; i++) {
       App.xhrArray[i].abort()
       App.xhrArray[i] = null
    }
    App.xhrArray = []
}

App.post = function (cmd, data, callback) {
    let xhr = new XMLHttpRequest()
    xhr.open('POST', App.apiurl + cmd)
    xhr.setRequestHeader('Content-Type', 'application/json')
    //!!!不要加这一句啊，要的话，调用App.postWithToken不就好了,不是所有的http请求都需要登录
    //response.addHeader("Access-Control-Allow-Headers", "Authorization");
    //xhr.setRequestHeader('Content-Length', JSON.stringify(data).length)
    xhr.send(JSON.stringify(data))

    App.xhrArray.push(xhr)
    xhr.onreadystatechange = function () {
        App.do_onreadystatechange(xhr, callback)
    };

    xhr.ontimeout = function (e) { };
    xhr.onerror = function (e) { };
    xhr.onprogress = function (e) { };
};

