// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
let App = require("App")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        mailList: {
            default: null,
            type: cc.ScrollView
        },
        mailItem: {
            default: null,
            type: cc.Prefab
        },
        noticeViewPanel: {
            default: null,
            type: cc.Prefab
        },
        mailData: Object,
        checkTypeType: 1,
        loginLogNode: {
            default: null,
            type: cc.Node
        },
        loginLogList: {
            default: null,
            type: cc.ScrollView
        },
        loginLogItem: {
            default: null,
            type: cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.checkTypeType = 1
        this.mailList.active = true
        this.loginLogNode.active = false
        let self = this
        {
            let getData = [
                "offset=" + 0
            ]
            let self = this

            App.getWithTokenData("/messages/list", getData, function (data) {
                if (parseInt(data.code) == 200) {
                    self.mailData = data
                    self.updateMailList()
                }
            })
        }
    },

    updateMailList() {
        this.mailList.content.removeAllChildren()
        for (let i = 0; i < this.mailData.data.content.length; i++) {
            if (this.checkTypeType == 1) {

            }
            else if (this.checkTypeType == 2) {
                if (this.mailData.data.content[i].state == 0) {

                }
                else {
                    continue;
                }
            }
            else if (this.checkTypeType == 3) {
                if (this.mailData.data.content[i].state == 1) {

                }
                else {
                    continue;
                }
            } else {
                continue;
            }

            let mailItem = cc.instantiate(this.mailItem)
            this.mailList.content.addChild(mailItem)

            let bg
            if (this.mailData.data.content[i].state == 0) {
                bg = "bg_notice_item_unread"
            }
            else if (this.mailData.data.content[i].state == 1) {
                bg = "bg_notice_item_read"
            }

            cc.loader.loadRes(bg, cc.SpriteFrame, function (err, spriteFrame) {
                if (err) {
                    cc.log(err)
                    return
                }
                mailItem.getComponent(cc.Sprite).spriteFrame = spriteFrame
            })

            mailItem.getChildByName("sender").getComponent(cc.Label).string = "发信人: " + this.mailData.data.content[i].user
            mailItem.getChildByName("content").getComponent(cc.Label).string = this.mailData.data.content[i].title
            mailItem.getChildByName("time").getComponent(cc.Label).string = this.mailData.data.content[i].createDate

            let viewNode = mailItem.getChildByName("view")

            var clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; // 这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "notice";// 这个是代码文件名
            clickEventHandler.handler = "tapView";
            clickEventHandler.customEventData = i;

            var button = viewNode.getComponent(cc.Button);
            button.clickEvents.push(clickEventHandler);
        }
    },

    updateLoginLogList(data) {
        this.loginLogList.content.removeAllChildren()
        for (let i = 0; i < data.length; i++) {
            let loginLogItem = cc.instantiate(this.loginLogItem)

            loginLogItem.getChildByName("time").getComponent(cc.Label).string = data[i].createDate
            loginLogItem.getChildByName("ip").getComponent(cc.Label).string = data[i].createIp

            this.loginLogList.content.addChild(loginLogItem)
        }
    },

    checkType(sender, customEventData) {
        this.checkTypeType = customEventData
        if (this.checkTypeType == 1 || this.checkTypeType == 2 || this.checkTypeType == 3) {
            this.updateMailList()
            this.mailList.active = true
            this.loginLogNode.active = false
        } else {
            let self = this
            this.mailList.active = false
            this.loginLogNode.active = true
            App.getWithToken("/messages/logs", function (data) {
                if (parseInt(data.code) == 200) {
                    self.updateLoginLogList(data.data)
                }
            })
        }
    },
    start() {

    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    tapView(sender, customEventData) {
        let noticeViewPanel = cc.instantiate(this.noticeViewPanel)
        noticeViewPanel.getComponent("noticeView").setup(this.mailData.data.content[customEventData])
        cc.find("Canvas").addChild(noticeViewPanel)
    }

    // update (dt) {},
});
