//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        conversionToDebitCard: {
            default: null,
            type: cc.Node
        },
        conversionToAlipay: {
            default: null,
            type: cc.Node
        },
        conversionRecord: {
            default: null,
            type: cc.Node
        },
        conversionRecordList: {
            default: null,
            type: cc.ScrollView
        },
        conversionRecordItem: {
            default: null,
            type: cc.Prefab
        },
        bindDebitCardPanel: {
            default: null,
            type: cc.Prefab
        },
        bindAlipayPanel: {
            default: null,
            type: cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.conversionToDebitCard.active = true
        this.conversionToAlipay.active = false
        this.conversionRecord.active = false
    },

    start() {

    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    checkConversion(sender, customEventData) {
        let index = parseInt(customEventData)
        switch (index) {
            case 1:
                {
                    this.conversionToDebitCard.active = true
                    this.conversionToAlipay.active = false
                    this.conversionRecord.active = false
                }
                break
            case 2:
                {
                    this.conversionToDebitCard.active = false
                    this.conversionToAlipay.active = true
                    this.conversionRecord.active = false
                }
                break
            case 3:
                {
                    this.conversionToDebitCard.active = false
                    this.conversionToAlipay.active = false
                    this.conversionRecord.active = true
                    for (let i = 0; i < 10; i++) {
                        this.conversionRecordList.content.addChild(cc.instantiate(this.conversionRecordItem))
                    }
                }
                break
        }
    },

    tapBindDebitCard(sender, customEventData) {
        let bindDebitCardPanel = cc.instantiate(this.bindDebitCardPanel)
        cc.find("Canvas").addChild(bindDebitCardPanel)
    },

    tapBindAlipay(sender, customEventData) {
        let bindAlipayPanel = cc.instantiate(this.bindAlipayPanel)
        cc.find("Canvas").addChild(bindAlipayPanel)
    }

    // update (dt) {},
});
