    //width height 显示区域的大小
    //row col 行列
    //step 跳动的格数
    //duration 每次动画的持续时间
    showLotteryAnimation(width, height, row, col, step, duration, heroIconSpriteFrameArray, heroCount) {
        //cc.log(heroIconSpriteFrameArray)
        this.heroIconItem.active = false
        let self = this
        let rootWidth = (width / col) * (col + 2)
        let rootHeight = (height / row) * (row + 2)
        //cc.log(rootWidth, rootHeight)//!!!不删
        this.lotteryAnimationRoot.setContentSize(cc.size(rootWidth, rootHeight))
        this.lotteryAnimationRoot.setAnchorPoint(cc.v2(0, 0))
        this.lotteryAnimationRoot.setPosition(cc.v2(-rootWidth / 2, -rootHeight / 2))

        // let heroCount = {
        //     "gok": 88,
        //     "lol": 142,
        //     "dota": 113
        // }

        let mask = this.lotteryAnimationRoot.getChildByName("mask")
        mask.removeAllChildren()

        let heroIconPath = "/shishile/" + this.gameType + "/"
        for (let i = 0; i < row + 2; i++) {
            for (let j = 0; j < col + 2; j++) {
                // let node = new cc.Node()
                // let sprite = node.addComponent(cc.Sprite)
                // let url = heroIconPath + Tools.getRandomInt(1, heroCount[this.gameType])
                // cc.loader.loadRes(url, cc.SpriteFrame, function (err, spriteFrame) {
                //     if (err) {
                //         return;
                //     }
                //     sprite.spriteFrame = spriteFrame
                // })
                // node.setAnchorPoint(0.5, 0.5)
                // node.setPosition(cc.v2(i * (width / row), j * (height / col)))
                // root.addChild(node)
                let heroIconItem = cc.instantiate(this.heroIconItem)
                heroIconItem.active = true

                for (let k = 0; k < 9; k++) {
                    let item = heroIconItem.getChildByName("item" + k)
                    let url = heroIconPath + Tools.getRandomInt(1, heroCount)
                    // cc.loader.loadRes(url, cc.SpriteFrame, function (err, spriteFrame) {
                    //     if (err) {
                    //         cc.log(err)
                    //         return;
                    //     }
                    //     item.getComponent(cc.Sprite).spriteFrame = spriteFrame
                    // })

                    let no = Tools.getRandomInt(1, heroCount)
                    item.getComponent(cc.Sprite).spriteFrame = heroIconSpriteFrameArray[no]
                }

                //cc.log(cc.v2(j * (width / col), i * (height / row)))
                let posX = (j - step + 1 / 2) * (width / col)
                let posY = (i - step + 1 / 2) * (height / row)
                heroIconItem.setPosition(cc.v2(posX, posY))
                mask.addChild(heroIconItem)
            }
        }

        let children = mask.children
        for (let i = 0; i < children.length; i++) {
            let randomSignX = Tools.getRandomInt(0, 2)
            let signX
            if (randomSignX == 0) {
                signX = 0
            } else if (randomSignX == 1) {
                signX = 1
            } else if (randomSignX == 2) {
                signX = -1
            }

            let randomSignY = Tools.getRandomInt(0, 2)
            let signY
            if (randomSignY == 0) {
                signY = 0
            } else if (randomSignY == 1) {
                signY = 1
            } else if (randomSignY == 2) {
                signY = -1
            }

            let posX = signX * (width / col)
            let posY = signY * (height / row)

            let moveBy = new cc.moveBy(duration, cc.v2(posX, posY))
            //children[i].runAction(moveBy)
        }
    },

    // // update (dt) {},

    onDestroy() {
        if (this.countdownFunc) {
            this.countdownComponent.unschedule(this.countdownFunc)
        }

        App.off("channel.LOTTEY")

        App.off("channel.LOTTEY_RESULT")

    }
