

const { ccclass, property } = cc._decorator;

@ccclass
export default class itemManager extends cc.Component {

    @property(cc.Label)
    labelList: cc.Label[] = [];

    @property
    text: string = 'hello';

    listX: number[][] = []


    updateData(data, i) {

        // cc.log(data);

        this.listX = [
            [-415, -125, 305, 200, 380],
            [-415, -150, 115, 380],
            [-424, -172, 60, 230, 380],
            [-413, -157, 54, 220, 366],
            [-413, -140, 74, 220, 366],
            [-365, 20, 305]
        ];

        this.labelList.forEach((e, index) => {
            e.node.active = false;
            if (this.listX[i][index]) {
                e.node.active = true;
                e.node.x = this.listX[i][index];

            }

        })

        this.setData(data, i);


    }

    setData(v, i) {
        // cc.log(i)
        switch (i) {
            case '0':

                break;

            case '1':
                this.labelList[0].string = v.betAmount;
                this.labelList[1].string = v.depositAmount;
                this.labelList[2].string = v.rebateAmount;
                this.labelList[3].string = v.winAmount;
                break;
            case '2':

                break;
            case '3':

                break;
            case '4':

                break;
            case '5':
                this.labelList[0].string = v.createDate;
                this.labelList[1].string = v.createIp;
                this.labelList[2].string = v.remark;
                break;
        }
    }
}
