// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        weipay: {
            default: null,
            type: cc.Node
        },
        alipay: {
            default: null,
            type: cc.Node
        },
        qqpay: {
            default: null,
            type: cc.Node
        },
        unionpay: {
            default: null,
            type: cc.Node
        },
        rechargeRecrd: {
            default: null,
            type: cc.Node
        },
        rechargeRecrdList: {
            default: null,
            type: cc.ScrollView
        },
        rechargeRecrdItem: {
            default: null,
            type: cc.Prefab
        },
        rechargeAmountPanel:{
            default: null,
            type: cc.Prefab
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        this.weipay.active = true
        this.alipay.active = false
        this.qqpay.active = false
        this.unionpay.active = false
        this.rechargeRecrd.active = false
    },

    start() {

    },

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    checkPay(sender, customEventData) {
        let index = parseInt(customEventData)
        switch (index) {
            case 1:
                {
                    this.weipay.active = true
                    this.alipay.active = false
                    this.qqpay.active = false
                    this.unionpay.active = false
                    this.rechargeRecrd.active = false
                }
                break
            case 2:
                {
                    this.weipay.active = false
                    this.alipay.active = true
                    this.qqpay.active = false
                    this.unionpay.active = false
                    this.rechargeRecrd.active = false
                }
                break
            case 3:
                {
                    this.weipay.active = false
                    this.alipay.active = false
                    this.qqpay.active = true
                    this.unionpay.active = false
                    this.rechargeRecrd.active = false
                }
                break
            case 4:
                {
                    this.weipay.active = false
                    this.alipay.active = false
                    this.qqpay.active = false
                    this.unionpay.active = true
                    this.rechargeRecrd.active = false
                }
                break
            case 5:
                {
                    this.weipay.active = false
                    this.alipay.active = false
                    this.qqpay.active = false
                    this.unionpay.active = false
                    this.rechargeRecrd.active = true
                    for(let i=0;i<10;i++)
                    {
                        this.rechargeRecrdList.content.addChild(cc.instantiate(this.rechargeRecrdItem))
                    }
                }
                break
        }
    },

    tapImmediatelyGoPay(sender, customEventData) {
        let rechargeAmountPanel = cc.instantiate(this.rechargeAmountPanel)
        rechargeAmountPanel.getComponent("rechargeAmount").setup(customEventData)
        cc.find("Canvas").addChild(rechargeAmountPanel)
    }

    // update (dt) {},
});
