let SDKTools = function () { };
let CryptoJS = require("CryptoJS")

let className = ""

SDKTools.init = function () {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS) {
        className = "SDKTools"
    }
    else if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
        className = "org/cocos/plugins/SDKTools"
    }
}

SDKTools.selectAlbumImage = function () {
    if (cc.sys.isNative) {
        let now = new Date()
        let nowStr = String(now.getTime())
        let writablePath = jsb.fileUtils.getWritablePath()
        let savePath = writablePath + String(CryptoJS.MD5(nowStr))

        if (cc.sys.isNative && cc.sys.os == cc.sys.OS_IOS) {
            let ret = jsb.reflection.callStaticMethod(className, "selectAlbumImage:", savePath)
            return ret
        }
        else if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
            let ret = jsb.reflection.callStaticMethod(className, "selectAlbumImage", "(Ljava/lang/String;)V", savePath);
            return ret
        }
    }
}

SDKTools.scanCard = function () {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
        let ret = jsb.reflection.callStaticMethod(className, "scanCard", "(Ljava/lang/String;)V", "foo");
        return ret
    }
}

SDKTools.copyToClipBoard = function (text) {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
        let ret = jsb.reflection.callStaticMethod(className, "copyToClipBoard", "(Ljava/lang/String;)V", text);
        return ret
    }
}

SDKTools.saveToPhotoAlbum = function () {
    if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
        let ret = jsb.reflection.callStaticMethod(className, "saveToPhotoAlbum", "(Ljava/lang/String;)V", "foo");
        return ret
    }
}

SDKTools.createQRImage = function (url, width, height) {
    if (cc.sys.isNative) {
        let writablePath = jsb.fileUtils.getWritablePath()
        let qrCodeSavePath = writablePath + "qrcode.jpg"
        if (cc.sys.isNative && cc.sys.os == cc.sys.OS_ANDROID) {
            let ret = jsb.reflection.callStaticMethod(className, "createQRImage", "(Ljava/lang/String;Ljava/lang/String;Ljava/lang/String;Ljava/lang/String;)V", url, qrCodeSavePath, width, height)
            return ret
        }
        else if (cc.sys.isNative && cc.sys.OS_IOS) {

        }
    }
}

module.exports = SDKTools