// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] https://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
let SDKTools = require('SDKTools')
let App = require("App")

cc.Class({
    extends: cc.Component,

    properties: {
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
        headImage: {
            default: null,
            type: cc.Sprite
        }
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_START, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_MOVE, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_END, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopPropagation()
            }
        })

        this.node.on(cc.Node.EventType.TOUCH_CANCEL, function (event, touch) {
            if (event.stopProgagation != undefined) {
                event.stopProgagation()
            }
        })

        let self = this
        cc.loader.load({ url: App.getHeadImageByUserId(App.myUser.id), type: "png" }, function (err, texture) {
            if (err) {
                return
            }
            let sp = new cc.SpriteFrame(texture)
            self.headImage.getComponent(cc.Sprite).spriteFrame = new cc.SpriteFrame(texture)
        });

        App.on("finishSelectImage", function (savePath, imageWidth, imageHeight) {
            cc.loader.load({ url: savePath, type: "binary" }, function (err, result) {
                App.postImage("/users/uploadAvatarStream", result, function (data) {
                    if (parseInt(data.code) == 200) {
                        cc.find("FadeMsg").getComponent("FadeMsg").show("上传头像成功")
                    } else {
                        cc.find("FadeMsg").getComponent("FadeMsg").show(data.message.zh)
                    }
                })
            })
        })
    },

    start() {

    },

    // update (dt) {},

    tapClose(sender, customEventData) {
        this.node.destroy()
    },

    tapChangeHeadImage(sender, customEventData) {
        if (cc.sys.isNative) {
            SDKTools.selectAlbumImage()
        }
        else {
            var gameCanvas = document.getElementById("GameCanvas")
            var div = document.createElement("div")
            div.style.position = "absolute"
            div.style.width = 140
            div.style.height = 140
            div.style.left = 140
            div.style.top = 140
            var form = document.createElement("form")
            form.enctype = "multipart/form-data"
            form.id = "uploadForm"
            var input = document.createElement("input")
            input.id = "upLoadInput"
            input.apccept = "image/*"
            input.type = "file"
            if (cc.sys.os == cc.sys.OS_ANDROID) {
                input.capture = "camera"
            }
            div.appendChild(form)
            form.appendChild(input)
            document.body.appendChild(div)

            var width = document.documentElement.clientWidth
            var height = document.documentElement.clientHeight
            cc.log(width, height)
            if (width < height) {
                div.style.transform = 'rotate(90deg)'
            }
            input.addEventListener('change', function (e) {
                //实例化表单数据对象，并将form对象作为入参传入
                let formData = new FormData(form)
                div.remove()
                //使用append方法添加key/value
                formData.append('orderId', 123456789)
                cc.log(formData)

                /// 实例化一个AJAX对象
                let xhr = new XMLHttpRequest()
                //创建一个 post 请求
                xhr.open('POST', '/m2015/visa/book/UploadAndBindMaterial')
                //注册相关事件回调处理函数
                xhr.onload = function (e) {
                    if (xhr.status == 200) {
                        //上传完成
                    } else {
                        //上传失败
                    }
                };

                xhr.ontimeout = function (e) { };
                xhr.onerror = function (e) { };
                xhr.upload.onprogress = function (e) { };

                //发送数据
                xhr.send(formData);
            }, false)
        }
    },

    tapEditNickName(sender, customEventData) {

    },

    tapSwitchAccount(sender, customEventData) {

    },

    onDestroy(){
        App.off("finishSelectImage")
    }
});
