let MyUser = {}

//传入参数只能为object类型
MyUser.setup = function (data) {
    if (data.username != undefined) {
        MyUser.username = data.username
    }

    if (data.nickname != undefined) {
        MyUser.nickname = data.nickname
    }

    if (data.truename != undefined) {
        MyUser.truename = data.truename
    }

    if (data.point != undefined) {
        MyUser.point = data.point
    }

    if (data.level != undefined) {
        MyUser.level = data.level
    }

    if (data.nextLevelPoint != undefined) {
        MyUser.nextLevelPoint = data.nextLevelPoint
    }

    if (data.coins != undefined) {
        MyUser.coins = data.coins
    }

    if (data.hasAvatar != undefined) {
        MyUser.hasAvatar = data.hasAvatar
    }

    if (data.balance != undefined) {
        MyUser.balance = data.balance
    }

    if (data.lastLoginDate != undefined) {
        MyUser.lastLoginDate = data.lastLoginDate
    }

    if (data.lastLoginLocation != undefined) {
        MyUser.lastLoginDate = data.lastLoginLocation
    }

    if (data.deposit != undefined) {
        MyUser.deposit = data.deposit
    }

    if (data.id != undefined) {
        MyUser.id = data.id
    }

    if(data.email != undefined){
        MyUser.email = data.email
    }
        
    if(data.birthdate != undefined){
        MyUser.birthdate = data.birthdate
    }
    
    if(data.hasSecureCode != undefined){
        MyUser.hasSecureCode = data.hasSecureCode
    }
    
    if(data.profile != undefined){
        MyUser.profile = data.profile
    }
    
    if(data.telephone != undefined){
        MyUser.telephone = data.telephone
    }

    MyUser.birthdate = data.birthdate

    MyUser.sex = data.sex
};

module.exports = MyUser